import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { DISTRICTS } from "./../utils/config";
import { parameterize } from "./../utils/parser";
import DocumentTitle from "../components/Extras/Header";

const DonorSearchPage = () => {

    const { t } = useTranslation("Common");

    return (
        <section className="p-5 sm:px-10 select-none bg-white mx-5 rounded-lg shadow-lg relative">
            <DocumentTitle title="Search Donors" />
            <h2 className="text-lg font-semibold">Search Donors</h2>
            <div className="w-24 bg-red-700 h-1 my-2" />
            <Link to="/requests">
                <button className="absolute right-0 top-0 select-none active:translate-y-0.5 mr-5 mt-5 rounded-md text-indigo-700 font-semibold">Back</button>
            </Link>
            <div className="flex flex-col">
                <h5 className="my-5 mt-1">Select your location</h5>
                <div className="flex items-center flex-wrap gap-4 justify-evenly">
                    {
                        DISTRICTS.map((e, i) => {
                            return (<Link to={"/donors/" + parameterize(e)} key={i} className="w-36 h-36 sm:w-32 sm:h-32 md:w-40 md:h-40 bg-white transition-transform transform hover:scale-110 flex flex-col justify-evenly items-center cursor-pointer rounded-md group shadow-md hover:ring-1 hover:ring-red-500 ease-in-out">
                                <img className="h-10" src={`/icons/districts/${parameterize(e)}.png`} alt="" />
                                <p className="text-sm md:text-base font-medium text-gray-600 group-hover:text-red-600">{t(e)}</p>
                            </Link>)
                        })
                    }
                </div>
            </div>
        </section>
    );
}

export default DonorSearchPage;