import { getGender } from "./../../utils/validator"
import verifiedIcon from "./../../assets/images/verified_badge.png"
import DonorShare from "./DonorShare";

const DonorsListItem = (props) => {
    const { name, bloodGroup, gender, phone } = props;

    return (
        <div className="flex items-center  flex-col md:flex-row border-dashed border-2 rounded-sm p-1 py-2">
            <p className="flex-1 text-center md:text-left md:pl-2 min-w-max my-1 capitalize">
                {name?.toLowerCase()}
                <img className="h-3 w-3 md:h-4 md:w-4 inline-block ml-2" src={verifiedIcon} alt="Verified" title="Verified" />
            </p>
            <div className="flex flex-1 min-w-max items-center justify-center md:justify-end md:mx-1 gap-2 my-1">
                <div className="py-1.5 px-4 bg-gray-50 text-center rounded-md font-medium text-red-600">{bloodGroup}</div>
                <div className="py-1.5 px-4 text-center bg-gray-50 rounded-md text-base font-medium text-indigo-700">{getGender(gender)}</div>
            </div>
            <div className="flex flex-row md:flex-1 w-full min-w-max items-center justify-center gap-2 my-1 px-3">
                <a href={"tel:+91" + phone} className="p-1.5 px-4 flex-1 bg-red-600 hover:bg-red-700 transform active:translate-y-0.5 justify-center rounded-md font-medium text-white cursor-pointer flex items-center gap-2">
                    <span>
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                            <path d="M2 3a1 1 0 011-1h2.153a1 1 0 01.986.836l.74 4.435a1 1 0 01-.54 1.06l-1.548.773a11.037 11.037 0 006.105 6.105l.774-1.548a1 1 0 011.059-.54l4.435.74a1 1 0 01.836.986V17a1 1 0 01-1 1h-2C7.82 18 2 12.18 2 5V3z" />
                        </svg>
                    </span>
                    <span>Call</span>
                </a>
                <DonorShare donor={props} />
            </div>
        </div>
    );
}

export default DonorsListItem;