import React from 'react';
import warningIcon from "./../assets/images/warning.svg"
import { useTranslation } from "react-i18next"

const SomethingWentWrongPage = (props) => {

    const { t } = useTranslation("Requests");
    const title = props.title || "Something went wrong";
    const message = props.message || "This usually happens when the action performed is not succeeded";

    return (
        <div>
            <section className="py-5 px-10 text-center select-none">
                <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                    <img className="h-40 mx-auto my-2" src={warningIcon} alt="Alert" />
                    <h2 className="font-medium text-lg">{t(title)}!</h2>
                    <p className="text-sm my-2 px-5 text-gray-600">{t(message)}</p>
                </div>
            </section>
        </div>
    );
}

export default SomethingWentWrongPage;