import RequestsCardPlaceholder from "./RequestsCardPlaceholder";

const RequestsUrgentLoader = () => {
    return (
        <>
            {
                [1, 2, 3, 4, 5, 6].map(e => (
                    <RequestsCardPlaceholder key={e} />
                ))
            }
        </>
    );
}

export default RequestsUrgentLoader;