import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { getRequestById } from "./../utils/database";
import { connect } from "react-redux";
import { updateRequests } from "./../cache/request"
import { getRequestModel } from '../utils/generator';
import SomethingWentWrongPage from "./../pages/SomethingWentWrongPage"
import RequestTimer from '../components/RequestTimer';
import Button from '../components/Button';
import RequestsViewLoader from '../components/Loader/RequestsViewLoader';
import RequestShare from '../components/RequestShare';


const RequestsViewPublicPage = ({ requests, updateRequests }) => {

    const { requestId } = useParams();
    const [isLoading, setIsLoading] = useState(true);
    const [isDeleted, setIsDeleted] = useState(false);

    useEffect(() => {

        const unsubscribe = getRequestById(requestId).onSnapshot(result => {
            if (result.exists)
                updateRequests(getRequestModel(result.data()));
            else
                setIsDeleted(true);
            setIsLoading(false);
        }, _ => {
            console.log("Error", _)
            setIsLoading(false);
        })

        return () => {
            unsubscribe();
        }

    }, [requestId, updateRequests])

    const request = requests[requestId]

    if (isLoading) return <RequestsViewLoader />

    if (isDeleted) return <SomethingWentWrongPage title="Request was deleted" message="The request has been deleted by the user" />
    if (!request) return <SomethingWentWrongPage title="Request not found" message="The request could have been deleted or expired" />

    return (
        <div>
            <section className="p-5 sm:px-10 select-none bg-white mx-5 rounded-lg shadow-lg relative">
                <h2 className="text-lg font-semibold">#{request.requestId}</h2>
                <div className="w-28 bg-red-700 h-1 my-2" />
                <Link to="/requests" className="absolute transform right-0 top-0 select-none active:translate-y-0.5 mr-5 mt-5 rounded-md text-indigo-700 font-semibold">Back</Link>
                <div className="flex flex-col">
                    <div className="flex justify-between w-full flex-wrap">
                        <div className="flex items-center mt-2"><span>Name: </span><p className="ml-2 font-medium">{request.fullName}</p></div>
                        <div className="flex items-center mt-2"><span>Phone: </span><a href={`tel:+91${request.phone}`} className="ml-2 font-medium text-indigo-800">+91{request.phone}</a></div>
                    </div>
                    <div className="my-3 grid gap-2 items-center justify-around grid-auto-fit">
                        <div className="p-2 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                            <h2 className="text-2xl font-bold text-red-600">{request.bloodGroup}</h2>
                            <span>Blood</span>
                        </div>
                        <div className="p-2 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                            <h2 className="text-2xl font-bold text-red-600">{request.age}</h2>
                            <span>Age</span>
                        </div>
                        <div className="p-2 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                            <h2 className="text-2xl font-bold text-red-600">{request.gender}</h2>
                            <span>Gender</span>
                        </div>
                        <div className="p-2 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                            <h2 className="text-2xl font-bold text-red-600">{request.units}</h2>
                            <span>Units</span>
                        </div>
                    </div>
                    <div className="rounded-md my-3 flex flex-col text-left">
                        <div className="font-medium">Hospital / Location</div>
                        <div>{request.hospital}, {request.location}</div>
                    </div>
                    <div className="rounded-md mb-3 flex flex-col text-left">
                        <div className="font-medium">Purpose</div>
                        <div>{request.purpose}</div>
                    </div>
                    <div className="flex items-center relative justify-center">
                        <span className="font-medium text-red-600"><RequestTimer needWithin={request.needWithin} /></span>
                        <div className="absolute right-0">
                            <RequestShare {...request} />
                        </div>
                    </div>
                </div>
            </section>
            <section className="p-5 mt-2 sm:px-10 select-none bg-white mx-5 rounded-lg shadow-lg">
                <h2 className="text-lg font-semibold">Login to Contribute</h2>
                <div className="w-28 bg-red-700 h-1 my-2" />
                <div className="mt-5 flex justify-center">
                    <Link to="/auth/login">
                        <Button value="Login" />
                    </Link>
                </div>
            </section>
        </div>
    );
}

const mapStateToProps = state => ({
    requests: state.requests
})

const mapDispatchToProps = dispatch => ({
    updateRequests: request => dispatch(updateRequests(request))
})

export default connect(mapStateToProps, mapDispatchToProps)(RequestsViewPublicPage);
