import SpinnerIcon from "./Icons/Spinner";

const Button = ({ onClick, className, value, isLoading, type, disabled }) => {
    return (
        <button
            disabled={isLoading || disabled}
            type={type}
            onClick={onClick}
            className={"bg-red-600 disabled:bg-red-500 disabled:cursor-not-allowed disabled:border-red-600 border-red-600 border-2 shadow-md hover:bg-red-700 hover:shadow-lg select-none transform active:translate-y-0.5 px-5 py-2 rounded-md text-white " + className}>
            {isLoading ? <SpinnerIcon className="h-5 w-5 mx-auto animate-spin" /> : value}
        </button>
    );
}

export default Button;