import React, { useState, useEffect } from 'react';
import { useQuery } from "./../utils/parser"
import Button from "./../components/Button";
import loadingIcon from "./../assets/images/waiting.svg"
import doneIcon from "./../assets/images/done.svg"
import errorIcon from "./../assets/images/error.svg"
import { auth } from "./../utils/firebase"
import { useHistory } from "react-router-dom";

const STATUS = {
    DONE: { image: doneIcon, title: "Verification Successful", subtitle: "Your email is now verified" },
    EXPIRED_ACTION_CODE: { image: errorIcon, title: "Verification Code Expired", subtitle: "This usually happens when you verify after long time" },
    INVALID_ACTION_CODE: { image: errorIcon, title: "Invalid Verification Code", subtitle: "This usually happens when wrong link has been clicked" },
    USER_DISABLED: { image: errorIcon, title: "Account Banned", subtitle: "Your account has been banned because of community guidelines" },
    USER_NOT_FOUND: { image: errorIcon, title: "Account Deleted", subtitle: "This usually happens when your account has been deleted after verification link sent" },
    LOADING: { image: loadingIcon, title: "Processing Request", subtitle: "This stage takes about 10-20 seconds" }
}


const EmailVerificationVerifyPage = () => {

    const [status, setStatus] = useState(STATUS.LOADING);
    const query = useQuery();
    const pageRouter = useHistory();
    const oobCode = query.get("oobCode");
    const mode = query.get("mode");

    useEffect(() => {

        const handleVerifyEmail = oobCode => {
            auth.applyActionCode(oobCode).then(result => {
                setStatus(STATUS.DONE);
            }).catch(({ code }) => {
                if (code === "auth/expired-action-code")
                    return setStatus(STATUS.EXPIRED_ACTION_CODE);
                if (code === "auth/user-disabled")
                    return setStatus(STATUS.USER_DISABLED);
                if (code === "auth/user-not-found")
                    return setStatus(STATUS.USER_NOT_FOUND);
                return setStatus(STATUS.INVALID_ACTION_CODE);
            })
        }

        const handleResetPassword = oobCode => {
            auth.verifyPasswordResetCode(oobCode).then(email => {
                pageRouter.replace(`/auth/forgot-password/reset?oobCode=${oobCode}&email=${email}`);
            }).catch(({ code }) => {
                if (code === "auth/expired-action-code")
                    return setStatus(STATUS.EXPIRED_ACTION_CODE);
                if (code === "auth/user-disabled")
                    return setStatus(STATUS.USER_DISABLED);
                if (code === "auth/user-not-found")
                    return setStatus(STATUS.USER_NOT_FOUND);
                return setStatus(STATUS.INVALID_ACTION_CODE);
            })
        }

        if (oobCode)
            switch (mode) {
                case 'resetPassword':
                    handleResetPassword(oobCode);
                    break;
                case 'verifyEmail':
                    handleVerifyEmail(oobCode);
                    break;
                default:
                    return setStatus(STATUS.INVALID_ACTION_CODE);
            }
        else
            return setStatus(STATUS.INVALID_ACTION_CODE);

        // eslint-disable-next-line
    }, [oobCode])


    const handleContinue = () => {
        window.location.href = "/auth/login";
    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-lg shadow-lg mx-auto px-2 py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl text-red-600">Welcome to Blood Assist!</h2>
                    <img className="h-36 w-36 mx-auto my-10" src={status.image} alt={status.title} />
                    <h2 className="font-medium text-lg">{status.title}</h2>
                    <p className="text-xs text-gray-500">{status.subtitle}</p>
                    <div className="px-5 my-5">
                        <Button
                            onClick={handleContinue}
                            isLoading={status.title === STATUS.LOADING.title}
                            className="w-full"
                            value="Continue"
                        />
                    </div>
                </div>
            </section>
        </div>
    );
}

export default EmailVerificationVerifyPage;