import { useState } from "react";
import { Link } from "react-router-dom"
import Button from "../components/Button";
import { auth } from "./../utils/firebase";
import { useTranslation } from "react-i18next";
import GoogleSignInButton from "../components/GoogleSignInButton";

const ERRORS = {
    UNKNOWN: "Someting went wrong! Try again",
    USER_NOT_FOUND: "Your account doesn't exist",
    WRONG_PASSWORD: "You have entered wrong password",
    USER_DISABLED: "Your account has been banned because of community guidelines",
    NO_ERROR: ""
}

const LoginPage = ({ location }) => {

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(location?.state?.message || ERRORS.NO_ERROR);
    const [formError, setFormError] = useState({ email: "", password: "" });
    const { t } = useTranslation("Auth");

    const handleSignInUser = (e) => {

        e.preventDefault();

        if (!email) return setFormError({ email: "Enter a valid Email!", password: "" });
        if (!password) return setFormError({ email: "", password: "Enter a valid Password!" });
        if (password.length < 6) return setFormError({ email: "", password: "Password should be atleast 6 characters" });

        setIsLoading(true);
        setFormError({ email: "", password: "" });

        auth.signInWithEmailAndPassword(email, password).catch(({ code }) => {
            setIsLoading(false);
            if (code === "auth/invalid-email")
                return setFormError({ email: "Enter a valid Email ID!" });
            if (code === "auth/user-disabled")
                return setError(ERRORS.USER_DISABLED);
            if (code === "auth/user-not-found")
                return setError(ERRORS.USER_NOT_FOUND);
            if (code === "auth/wrong-password")
                return setError(ERRORS.WRONG_PASSWORD);
            return setError(ERRORS.UNKNOWN);
        })
    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl">{t("Welcome Back")}!</h2>
                    {error && <div className="p-2 bg-red-100 mx-5 my-2 text-gray-800 rounded-sm">{error}</div>}
                    <form onSubmit={handleSignInUser}>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <label className="text-sm my-2 text-gray-600" htmlFor="email">{t("Email Address")}</label>
                            <input
                                value={email}
                                onChange={({ target: { value } }) => setEmail(value)}
                                type="email"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                placeholder={t("Enter Your Email")} id="email" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["email"]}</span>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <label className="text-sm my-2 text-gray-600" htmlFor="password">{t("Password")}</label>
                            <input
                                value={password}
                                onChange={({ target: { value } }) => setPassword(value)}
                                type="password"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                placeholder={t("Enter Password")} id="password" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["password"]}</span>
                        </div>
                        <div className="my-5 flex flex-col mx-5 items-start">
                            <Link to="/auth/forgot-password" className="text-indigo-900 font-medium">{t("Forgot Password")} ?</Link>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <Button isLoading={isLoading} type="submit" value={t("Login")} className="w-full" />
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <GoogleSignInButton />
                        </div>
                        <div className="flex flex-wrap justify-center items-center mt-5">
                            <span className="mr-1 text-sm">{t("Not yet registered")} ?</span>
                            <Link to="/auth/register" className="flex flex-col items-start font-medium text-red-600">
                                {t("Register")}
                            </Link>
                        </div>
                    </form>
                </div>
            </section>
        </div>
    );
}

export default LoginPage;