import React, { useState } from 'react';
import Button from "./../components/Button";
import OutlineButton from "./../components/OutlineButton";
import { Link, Redirect, useHistory } from "react-router-dom";
import { BLOOD_GROUPS, DISTRICTS, GENDERS } from "./../utils/config";
import { updateUserDataByID, updateDonorStatus } from "./../utils/database";
import { connect } from "react-redux";
import Date from '../components/Forms/Date';
import moment from 'moment';

const FORM_ERROR = { location: "", dob: "", gender: "", bloodGroup: "" }

const ProfileUpdatePage = ({ user, id, oldLocation, oldDOB, oldBloodGroup, oldGender, isUpdateRequired }) => {

    const [location, setLocation] = useState(oldLocation);
    const [dob, setDob] = useState((oldDOB && moment(oldDOB)) || moment("1990"))
    const [gender, setGender] = useState(oldGender)
    const [bloodGroup, setBloodGroup] = useState(oldBloodGroup)
    const [error, setError] = useState("");
    const [formError, setFormError] = useState(FORM_ERROR);
    const [isLoading, setIsLoading] = useState(false);
    const pageRouter = useHistory();

    if (isUpdateRequired === null) return <Redirect to="/profile" />;

    const handleUpdateUser = (e) => {
        e.preventDefault();

        if (!location || !DISTRICTS.includes(location))
            return setFormError({ ...FORM_ERROR, location: "Choose your location" });
        if (!gender || !GENDERS.includes(gender))
            return setFormError({ ...FORM_ERROR, gender: "Choose your Gender" });
        if (!bloodGroup || !BLOOD_GROUPS.includes(bloodGroup))
            return setFormError({ ...FORM_ERROR, bloodGroup: "Choose your Blood Group" });
        if (!dob)
            return setFormError({ ...FORM_ERROR, dob: "Choose a Valid Date" });

        setFormError(FORM_ERROR);
        setIsLoading(true);

        updateUserDataByID(id, { location, dob: dob.toDate(), gender, bloodGroup })
            .then(_ => pageRouter.replace("/profile"))
            .catch(_ => {
                setError("Error in updating the details. Try again later");
                setIsLoading(false);
            })
        updateDonorStatus(user, true).catch(_ => { })
    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl">Update Your Profile</h2>
                    <p className="text-xs text-gray-600 my-1">Fill up to date information</p>
                    {error && <div className="p-2 bg-red-100 mx-5 my-2 text-gray-800 rounded-sm">{error}</div>}
                    <form onSubmit={handleUpdateUser}>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="location">Address</label>
                            <select
                                onChange={({ target: { value } }) => setLocation(value)}
                                value={location}
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm" id="location">
                                <option value="" disabled={true}>Select Your Location</option>
                                {
                                    DISTRICTS.map(e => (
                                        <option key={e} value={e}>{e}</option>
                                    ))
                                }
                            </select>
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["location"]}</span>
                        </div>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="dob">Date of Birth</label>
                            <Date
                                className="w-full border-0"
                                value={dob}
                                onChange={(value) => setDob(value)}
                            />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["dob"]}</span>
                        </div>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="gender">Gender</label>
                            <select
                                onChange={({ target: { value } }) => setGender(value)}
                                value={gender}
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                id="gender">
                                <option value="" disabled={true}>Select Your Gender</option>
                                <option value="M">Male</option>
                                <option value="F">Female</option>
                                <option value="T">Transgender</option>
                            </select>
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["gender"]}</span>
                        </div>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="bloodGroup">Blood Group</label>
                            <select
                                onChange={({ target: { value } }) => setBloodGroup(value)}
                                value={bloodGroup}
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                id="bloodGroup">
                                <option value="" disabled={true}>Select Your Blood Group</option>
                                {
                                    BLOOD_GROUPS.map(e => (
                                        <option key={e} value={e}>{e}</option>
                                    ))
                                }
                            </select>
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["bloodGroup"]}</span>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <Button isLoading={isLoading} className="w-full" value="Update" />
                        </div>
                        <Link to="/profile" className="my-2 flex flex-col mx-5 items-start">
                            <OutlineButton type="button" className="w-full" value="Cancel" />
                        </Link>
                    </form>
                </div>
            </section >
        </div >
    )
}

const mapStateToProps = state => ({
    user: state.auth,
    id: state.auth.id,
    isUpdateRequired: state.auth.isUpdateRequired,
    oldLocation: state.auth.location || "",
    oldDOB: state.auth.dob || "",
    oldBloodGroup: state.auth.bloodGroup || "",
    oldGender: state.auth.gender || ""
})

export default connect(mapStateToProps)(ProfileUpdatePage);