import { useLocation } from "react-router-dom";

export function useQuery() {
    return new URLSearchParams(useLocation().search);
}

export function parameterize(word) {
    return word.split(" ").join("_").toLowerCase();
}

export function humanize(word) {
    return word
        .split("_")
        .map(e => e[0].toUpperCase() + e.substr(1))
        .join(" ");
}

export const getListFromObject = obj => Object.keys(obj).map(e => obj[e]);