import React, { useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from "./Button";
import { getRequestById } from "./../utils/database"

const RequestsViewDelete = ({ request, className }) => {

    const [isOpen, setIsOpen] = useState(false);

    const handleDeleteRequest = () => {
        getRequestById(request.id).delete();
    }

    return (
        <>
            <span title="Delete" className={className} onClick={_ => setIsOpen(true)}>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                </svg>
            </span>
            <Dialog
                open={isOpen}
                onClose={_ => setIsOpen(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">Are you sure about this?</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <p className="text-gray-700">You are about to delete the request!</p>
                        <p className="text-sm">If you delete more than 3 requests, your account will be banned</p>
                        <p className="text-xs">Our volunteers are working hard, so please post only when there is a need.</p>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDeleteRequest} value="Yes" className="pt-1 pb-1" />
                    <Button onClick={_ => setIsOpen(false)} value="Cancel" className="pt-1 pb-1" />
                </DialogActions>
            </Dialog>
        </>
    );
}

export default RequestsViewDelete;