import { database, firestore, dynamicLink } from "./firebase";
import { timeStampValue, databaseTimestampValue } from "./firebase";
import { generateRequestID, getDonorUpdateModel } from "./generator"
import { BLOOD_GROUPS } from "./config"
import { parameterize } from "./parser"

export const getUserDataByID = (id) => {
    return database.ref("Users").child(id).get();
}

export const getPublicUserDataByID = (id) => {
    return Promise.all([
        database.ref("Users").child(id).child("id").get(),
        database.ref("Users").child(id).child("name").get(),
        database.ref("Users").child(id).child("gender").get(),
        database.ref("Users").child(id).child("location").get(),
        database.ref("Users").child(id).child("bloodGroup").get(),
        database.ref("Users").child(id).child("dob").get(),
        database.ref("Users").child(id).child("reputation").get(),
    ]);
}

export const streamUserDataByID = (id, success, error) => {
    return database.ref("Users").child(id).on("value", success, error)
}

export const updateUserDataByID = (id, data) => {
    return database.ref("Users").child(id).update(data);
}

export const createNewRequest = (data) => {
    const id = firestore.collection("Requests").doc().id;
    return firestore.collection("Requests").doc(id).set({
        ...data,
        id: id,
        status: "UNSOLVED",
        isVerified: false,
        requestId: generateRequestID(),
        timeStamp: timeStampValue()
    });
}

export const getAllRequests = () => {
    return firestore.collection("Requests")
        .where("status", "==", "UNSOLVED")
        .orderBy("needWithin")
        .limit(50)
        .get()
}

export const getAllUserRequests = (id) => {
    return firestore.collection("Requests")
        .where("userId", "==", id)
        .orderBy("timeStamp", "desc")
        .limit(10)
        .get()
}

export const getAllRecentlySolvedRequests = () => {
    return firestore.collection("Requests")
        .where("status", "==", "SOLVED")
        .orderBy("closedOn", "desc")
        .limit(10)
        .get()
}

export const getRequestById = (id) => {
    return firestore.collection("Requests")
        .doc(id);
}

export const updateRequestById = (id, data) => {
    return getRequestById(id).update(data)
}

export const getShortLink = (link, title, desc) => {
    return dynamicLink.createLink({
        dynamicLinkInfo: {
            domainUriPrefix: 'https://bloodassist.org/r',
            link,
            socialMetaTagInfo: {
                socialTitle: title,
                socialDescription: desc
            },
        },
        suffix: {
            option: 'SHORT',
        },
    });
}

export const addDonorByRequestId = (requestId, data) => {
    const { id } = getRequestById(requestId).collection('Donors').doc();
    return getRequestById(requestId).collection('Donors').doc(id).set({
        id,
        ...data
    });
}

export const removeDonorByRequestId = (requestId, donorId) => {
    return getRequestById(requestId).collection('Donors').doc(donorId).delete();
}

export const getDonorsByBloodType = (district, bloodType) => {

    if (bloodType === "Any")
        return database.ref("Donors").child(district).orderByChild("lastUpdated").limitToLast(50).once("value");

    if (!BLOOD_GROUPS.includes(bloodType))
        return Promise.reject("Not a Valid Blood Type");

    return database.ref("Donors").child(district).orderByChild("bloodGroup").equalTo(bloodType).limitToFirst(50).once("value");
}


export const getDonorStatus = (user) => {
    const { phone, location } = getDonorUpdateModel(user);

    if (!phone || Number.isNaN(phone) || !location)
        return Promise.reject("Invalid Data");

    return database.ref("Donors")
        .child(parameterize(location))
        .child(phone).get();
}

export const updateDonorStatus = (user, toRemove) => {
    const { phone, location } = getDonorUpdateModel(user);

    if (!phone || Number.isNaN(phone) || !location)
        return Promise.reject("Invalid Data");

    if (toRemove)
        return database.ref("Donors")
            .child(parameterize(location))
            .child(phone).remove();

    const payload = { ...getDonorUpdateModel(user), lastUpdatedOn: databaseTimestampValue }

    return database.ref("Donors")
        .child(parameterize(location))
        .child(payload.phone).update(payload);
}