import { useState, useEffect } from "react";
import { BLOOD_GROUPS } from "./../../utils/config";
import DonorsListItem from "./DonorsListItem";
import Pagination from "./../Extras/Pagination";
import { getDonorsByBloodType } from "../../utils/database";
import { getListFromObject } from "../../utils/parser";
import DonorsNotFound from "./DonorsNotFound";

const DonorsListWithFilter = ({ district }) => {

    const [selectedBloodFilter, setSelectedBloodFilter] = useState("Any");
    const [donors, setDonors] = useState({});
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        setIsLoading(true);
        getDonorsByBloodType(district, selectedBloodFilter)
            .then(e => {
                setDonors(e.val() || {});
                setIsLoading(false);
            }).catch(_ => { })
    }, [district, selectedBloodFilter])

    const list = getListFromObject(donors);

    return (
        <>
            <h2 className="text-sm font-semibold my-1">Filters</h2>
            <div className="flex items-center flex-wrap gap-4">
                <select
                    value={selectedBloodFilter}
                    onChange={({ target: { value } }) => setSelectedBloodFilter(value)}
                    className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 flex-1 text-sm">
                    <option value="" disabled={true}>Select Your Blood Group</option>
                    <option value="Any" >Any Blood Group</option>
                    {
                        BLOOD_GROUPS.map(e => (
                            <option key={e} value={e}>{e}</option>
                        ))
                    }
                </select>
                <select className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 flex-1 text-sm">
                    <option value="available">Available</option>
                </select>
            </div>
            <main className="my-2 flex flex-col py-2 gap-2">
                <Pagination
                    isLoading={isLoading}
                    component={DonorsListItem}
                    notFoundComponent={DonorsNotFound}
                    list={list}
                    pageLimit={10} />
            </main>
        </>
    );
}

export default DonorsListWithFilter;