import React, { useState, useEffect } from 'react';
import { connect } from "react-redux";
import flagIcon from "./../assets/images/flag.svg";
import emailIcon from "./../assets/images/mail.svg";
import noAvatarIcon from "./../assets/images/no_avatar.png";
import heartIcon from "./../assets/images/heart.png";
import { getAgeFromDOB } from '../utils/validator';
import { getPublicUserDataByID } from '../utils/database';
import { useParams, Redirect } from "react-router-dom";

const ProfilePage = ({ currentUserId }) => {

    const [user, setUser] = useState({});
    const { userId } = useParams();

    useEffect(() => {

        if (currentUserId !== userId)
            getPublicUserDataByID(userId).then(e => {
                const data = e.reduce((acc, curr) => ({ ...acc, [curr.key]: curr.val() }), {})
                setUser(data);
            }).catch(_ => { });

    }, [userId, currentUserId])

    if (currentUserId === userId) return <Redirect to="/profile" />

    const makeClean = (key) => {
        return user[key] || "N/A";
    }

    return (
        <div>
            <section className="p-5 sm:px-10 select-none bg-white mx-5 rounded-lg shadow-lg relative">
                <h2 className="text-lg font-semibold">User Profile</h2>
                <div className="w-20 bg-red-700 h-1 my-2" />
                <div className="flex flex-col sm:flex-row sm:items-center items-start flex-wrap my-5">
                    <div className="flex items-stretch flex-1">
                        <img className="border-2 border-red-700 border-dashed h-14 w-14 md:h-20 md:w-20 rounded-full shadow-lg" src={user?.image || noAvatarIcon} alt="Profile" />
                        <div className="flex flex-col pl-5 justify-center">
                            <div className="md:text-2xl mb-1 md:mb-2 text-lg text-gray-800">{makeClean("name")}</div>
                            <div className="flex items-center">
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-red-600" viewBox="0 0 20 20" fill="currentColor">
                                    <path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
                                </svg>
                                <span className="ml-1 md:text-base text-sm">{makeClean("location")}</span>
                            </div>
                        </div>
                    </div>
                    <div className="flex items-center md:flex-col md:items-start justify-center pt-5 md:pt-0">
                        <h2 className="text-base font-normal md:mb-1 mr-2 md:mr-0">Reputation</h2>
                        <div className="flex items-center">
                            <span className="text-xl md:text-3xl mr-1 font-semibold">{makeClean("reputation")}</span>
                            <img className="h-6 w-6 md:w-8 md:h-8" src={heartIcon} alt="Reputation" />
                        </div>
                    </div>
                </div>
                <h2 className="text-lg font-semibold">About</h2>
                <div className="w-10 bg-red-700 h-1 my-2" />
                <div className="my-5 grid grid-cols-3 gap-x-2 items-center justify-around">
                    <div className="p-5 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                        <h2 className="text-2xl font-bold text-red-600">{makeClean("bloodGroup")}</h2>
                        <span>Blood</span>
                    </div>
                    <div className="p-5 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                        <h2 className="text-2xl font-bold text-red-600">{getAgeFromDOB(user.dob)}</h2>
                        <span>Age</span>
                    </div>
                    <div className="p-5 flex flex-col items-center hover:bg-gray-100 ring-1 ring-gray-200 rounded-md">
                        <h2 className="text-2xl font-bold text-red-600">{makeClean("gender")}</h2>
                        <span>Gender</span>
                    </div>
                </div>
                <h2 className="text-lg font-semibold">Contact</h2>
                <div className="w-12 bg-red-700 h-1 my-2" />
                <div className="py-2">
                    <div className="flex flex-col items-start">
                        <label className="text-xs my-2 text-gray-600">Email</label>
                        <div className="rounded-sm w-full text-sm flex items-center relative">
                            <img className="absolute h-auto cursor-pointer w-6 rounded-sm mx-2" src={emailIcon} alt="Email" />
                            <span className="text-gray-600 text-xs md:text-sm rounded-sm select-all outline-none ring-2 ring-gray-400 focus:ring-red-500 flex-1 p-2 pl-12">Hidden because of Privacy Settings</span>
                        </div>
                    </div>
                    <div className="flex flex-col items-start">
                        <label className="text-xs my-2 text-gray-600">Phone</label>
                        <div className="rounded-sm w-full text-sm flex items-center relative">
                            <img className="absolute h-auto cursor-pointer w-6 rounded-sm mx-2" src={flagIcon} alt="Indian Flag" />
                            <span className="text-gray-600 text-xs md:text-sm rounded-sm select-all outline-none ring-2 ring-gray-400 focus:ring-red-500 flex-1 p-2 pl-12">Hidden because of Privacy Settings</span>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}


const mapStateToProps = state => ({
    currentUserId: state.auth.id
})

export default connect(mapStateToProps)(ProfilePage);