import React, { useState } from 'react';
import flagIcon from "./../assets/images/flag.svg";
import timerIcon from "./../assets/images/timer.png";
import Button from '../components/Button';
import { createNewRequest } from '../utils/database';
import { Link, useHistory } from "react-router-dom";
import { BLOOD_GROUPS, DISTRICTS } from "./../utils/config";
import { connect } from "react-redux";
import DateTime from '../components/Forms/DateTime';
import moment from "moment";
import { useTranslation } from "react-i18next";

const FORM_ERROR = { fullName: "", age: "", gender: "", bloodGroup: "", hospital: "", units: "", phone: "", purpose: "", location: "", needWithin: "" }

const RequestsNewPage = ({ id }) => {

    const [fullName, setFullName] = useState("");
    const [age, setAge] = useState("");
    const [gender, setGender] = useState("");
    const [bloodGroup, setBloodGroup] = useState("");
    const [hospital, setHospital] = useState("");
    const [location, setLocation] = useState("");
    const [units, setUnits] = useState("");
    const [phone, setPhone] = useState("");
    const [purpose, setPurpose] = useState("");
    const [needWithin, setNeedWithin] = useState(moment().add(30, "minutes"));
    const [formError, setFormError] = useState(FORM_ERROR);
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState("");
    const pageRouter = useHistory();
    const { t } = useTranslation("Requests");

    const handlePostNewRequest = (e) => {
        e.preventDefault();

        if (!fullName) {
            document.getElementById("name")?.focus();
            return setFormError({ ...FORM_ERROR, fullName: "Need Patient's Name" })
        }
        if (!age) {
            document.getElementById("age")?.focus();
            return setFormError({ ...FORM_ERROR, age: "Need Patient's Age" })
        }
        if (!gender) {
            document.getElementById("gender")?.focus();
            return setFormError({ ...FORM_ERROR, gender: "Need Patient's Gender" })
        }
        if (!hospital) {
            document.getElementById("hospital")?.focus();
            return setFormError({ ...FORM_ERROR, hospital: "Need Patient's Hospital" })
        }
        if (!location) {
            document.getElementById("location")?.focus();
            return setFormError({ ...FORM_ERROR, location: "Need Patient's Location" })
        }
        if (!DISTRICTS.includes(location)) {
            document.getElementById("location")?.focus();
            return setFormError({ ...FORM_ERROR, location: "Invalid Location" })
        }
        if (!bloodGroup) {
            document.getElementById("bloodGroup")?.focus();
            return setFormError({ ...FORM_ERROR, bloodGroup: "Need Patient's Blood Group" })
        }
        if (!BLOOD_GROUPS.includes(bloodGroup) && bloodGroup !== "Any") {
            document.getElementById("bloodGroup")?.focus();
            return setFormError({ ...FORM_ERROR, bloodGroup: "Invalid Blood Group" })
        }
        if (!units) {
            document.getElementById("units")?.focus();
            return setFormError({ ...FORM_ERROR, units: "Need Units between 1 and 50" })
        }
        if (!phone) {
            document.getElementById("phone")?.focus();
            return setFormError({ ...FORM_ERROR, phone: "Need Attender's Contact No" })
        }
        if (phone.length !== 10) {
            document.getElementById("phone")?.focus();
            return setFormError({ ...FORM_ERROR, phone: "Enter a valid 10-digit number" })
        }
        if (!purpose) {
            document.getElementById("purpose")?.focus();
            return setFormError({ ...FORM_ERROR, purpose: "Give a small note why it's needed" })
        }
        if (needWithin.diff(moment(), "minutes") <= 15)
            return setFormError({ ...FORM_ERROR, needWithin: "Need at least 15 minutes time" })

        setFormError(FORM_ERROR);

        setIsLoading(true);

        createNewRequest({ userId: id, fullName, age, gender, hospital, bloodGroup, units: parseInt(units), phone, purpose, location, needWithin: needWithin.toDate() })
            .then(_ => { pageRouter.replace("/requests"); })
            .catch(_ => {
                setError("Your request has not been posted. Please inform us to fix this")
                setIsLoading(false);
                document.body.scrollTop = 0;
                document.documentElement.scrollTop = 0;
            })
    }

    return (
        <section className="p-5 sm:px-10 bg-white mx-auto relative rounded-lg shadow-lg max-w-6xl">
            <h2 className="text-lg font-semibold mt-3">{t("New Blood Request")}</h2>
            <div className="w-24 bg-red-700 h-1 my-2" />
            <Link to="/requests">
                <button className="absolute right-0 top-0 active:translate-y-0.5 mr-6 mt-6 rounded-md text-indigo-700 font-semibold">{t("Cancel")}</button>
            </Link>
            <form onSubmit={handlePostNewRequest}>
                {error && <div className="p-2 bg-red-100 mx-5 my-2 text-gray-800 rounded-md">{error}</div>}
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="name">
                        <span className="font-medium text-red-900">{t("Patient's Name")}</span>
                        <p className="ml-1 text-xs">({t("Patient/Attender's Name")})</p></label>
                    <input
                        value={fullName}
                        onChange={({ target: { value } }) => setFullName(value)}
                        type="text"
                        className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                        placeholder={t("Enter Name")} id="name" />
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["fullName"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="age">
                        <span className="font-medium text-red-900">{t("Age")}</span>
                        <p className="ml-1 text-xs">({t("Patient's approx age")})</p>
                    </label>
                    <input
                        value={age}
                        onChange={({ target: { value } }) => setAge(value)}
                        type="number" min={1} max={110}
                        className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                        placeholder={t("Enter Your Age")} id="age" />
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["age"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="gender">
                        <span className="font-medium text-red-900">{t("Gender")}</span>
                        <p className="ml-1 text-xs">({t("Patient's Gender")})</p>
                    </label>
                    <select
                        value={gender}
                        onChange={({ target: { value } }) => setGender(value)}
                        className="w-full p-2 px-2 text-gray-700 outline-none ring-2 ring-gray-400 focus:ring-red-500 rounded-sm" name="gender" id="gender">
                        <option value="" disabled={true}>{t("Select Your Gender")}</option>
                        <option value="M">{t("Male")}</option>
                        <option value="F">{t("Female")}</option>
                        <option value="T">{t("Transgender")}</option>
                    </select>
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["gender"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="hospital">
                        <span className="font-medium text-red-900">{t("Hospital Name")}</span>
                        <p className="ml-1 text-xs">({t("Hospital in which patient takes treatment")})</p>
                    </label>
                    <input
                        value={hospital}
                        onChange={({ target: { value } }) => setHospital(value)}
                        type="text"
                        className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                        placeholder={t("Enter Your Hospital")} id="hospital" />
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["hospital"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="location">
                        <span className="font-medium text-red-900">{t("Location")}</span>
                        <p className="ml-1 text-xs">({t("Hospital's Location")})</p>
                    </label>
                    <select
                        value={location}
                        onChange={({ target: { value } }) => setLocation(value)}
                        className="w-full p-2 px-2 text-gray-700 outline-none ring-2 ring-gray-400 focus:ring-red-500 rounded-sm" name="location" id="location">
                        <option value="" disabled={true}>{t("Select Your Location")}</option>
                        {
                            DISTRICTS.map(e => (
                                <option key={e} value={e}>{t(e)}</option>
                            ))
                        }
                    </select>
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["location"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="blood_group">
                        <span className="font-medium text-red-900">{t("Required Blood Group")}</span>
                        <p className="ml-1 text-xs">({t("Blood Group that patient needs")})</p>
                    </label>
                    <select
                        value={bloodGroup}
                        onChange={({ target: { value } }) => setBloodGroup(value)}
                        className="w-full p-2 px-2 text-gray-700 outline-none ring-2 ring-gray-400 focus:ring-red-500 rounded-sm" name="blood_group" id="blood_group">
                        <option value="" disabled={true}>{t("Select Your Blood Group")}</option>
                        <option value="Any" >{t("Any Blood Group")}</option>
                        {
                            BLOOD_GROUPS.map(e => (
                                <option key={e} value={e}>{e}</option>
                            ))
                        }
                    </select>
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["bloodGroup"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="units">
                        <span className="font-medium text-red-900">{t("Required Units")}</span>
                        <p className="ml-1 text-xs">({t("No. of Blood Units needed")})</p>
                    </label>
                    <input
                        value={units}
                        onChange={({ target: { value } }) => setUnits(value)}
                        type="number"
                        min={1} max={50}
                        className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                        placeholder={t("Enter Required Units")} id="units" />
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["units"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600 flex items-center" htmlFor="phone">
                        <span className="font-medium text-red-900">{t("Phone")}</span>
                        <p className="ml-1 text-xs">({t("Patient/Attender's Phone")})</p>
                    </label>
                    <div className="rounded-sm w-full text-sm flex items-center relative">
                        <img className="absolute h-auto cursor-pointer w-6 rounded-sm mx-2" src={flagIcon} alt="Flag" />
                        <input
                            value={phone}
                            onChange={({ target: { value } }) => setPhone(value)}
                            type="number"
                            className="rounded-sm outline-none ring-2 ring-gray-400 focus:ring-red-500 flex-1 p-2 pl-12"
                            placeholder={t("Enter Your Phone")} id="phone" />
                    </div>
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["phone"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="purpose">{t("Purpose")}</label>
                    <textarea
                        value={purpose}
                        onChange={({ target: { value } }) => setPurpose(value)}
                        className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm pb-16"
                        placeholder={t("Purpose Placeholder")} id="purpose" />
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["purpose"]}</span>
                </div>
                <div className="my-3 flex flex-col mx-5 items-start">
                    <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="needwithin">{t("Needed within")}</label>
                    <div className="rounded-sm w-full text-sm flex items-center relative">
                        <img className="absolute h-auto cursor-pointer w-6 rounded-sm mx-2" src={timerIcon} alt="Timer" />
                        <DateTime
                            className="w-full border-0"
                            value={needWithin}
                            onChange={(value) => setNeedWithin(value)}
                        />
                    </div>
                    <span className="text-xs mt-2 text-red-600 font-medium">{formError["needWithin"]}</span>
                </div>
                <div className="my-5 flex flex-col mx-5 items-start">
                    <Button isLoading={isLoading} value={t("Post Request")} className="w-full" />
                </div>
            </form>
        </section>
    );
}

const mapStateToProps = state => ({
    id: state.auth.id
})

export default connect(mapStateToProps)(RequestsNewPage);