import React from 'react';
import { getGender } from '../utils/validator';
import { useTranslation } from 'react-i18next';
import RequestTimer from './RequestTimer';

const RequestCard = ({ request }) => {

    const { t } = useTranslation("Requests")
    const { fullName, location, gender, bloodGroup, units, needWithin } = request;

    return (
        <div className="p-3 py-5 bg-white rounded-md shadow-lg flex flex-col transform cursor-pointer border-b-2 hover:border-red-700 hover:scale-105 transition-transform">
            <header className="flex items-center justify-between">
                <div className="flex items-center">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="red">
                        <path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
                    </svg>
                    <span className="text-sm ml-2 text-red-600">{t(location)}</span>
                </div>
            </header>
            <p className="mt-5 text-center font-medium text-lg">{fullName}</p>
            <p className="mt-1 text-center font-medium text-gray-600">{t(getGender(gender))}</p>
            <div className="flex flex-wrap items-center justify-center mb-2">
                <div className="p-2 px-4 bg-gray-50 text-center rounded-md my-1 font-medium text-red-600 mx-3.5">{bloodGroup}</div>
                <div className="p-1.5 px-4 text-center bg-gray-50 rounded-md text-base font-medium text-indigo-700">{units} {t("Units")}</div>
            </div>
            <div className="text-center rounded-md font-semibold text-gray-500 text-sm mx-auto">
                <RequestTimer needWithin={needWithin} />
            </div>
        </div>

    );
}

export default RequestCard;