import React, { useState } from 'react';
import { useQuery } from "./../utils/parser"
import Button from "./../components/Button";
import loadingIcon from "./../assets/images/waiting.svg"
import doneIcon from "./../assets/images/done.svg"
import errorIcon from "./../assets/images/error.svg"
import { auth } from "./../utils/firebase"
import { useHistory } from "react-router-dom";
import privacyIcon from "./../assets/images/privacy.svg";

const STATUS = {
    DONE: { image: doneIcon, title: "Reset Successful", subtitle: "Your password has been updated. You can login now" },
    EXPIRED_ACTION_CODE: { image: errorIcon, title: "Reset Code Expired", subtitle: "This usually happens when you reset after long time" },
    INVALID_ACTION_CODE: { image: errorIcon, title: "Invalid Reset Code", subtitle: "This usually happens when wrong link has been clicked" },
    USER_DISABLED: { image: errorIcon, title: "Account Banned", subtitle: "Your account has been banned because of community guidelines" },
    USER_NOT_FOUND: { image: errorIcon, title: "Account Deleted", subtitle: "This usually happens when your account has been deleted after reset link sent" },
    LOADING: { image: loadingIcon, title: "Processing Request", subtitle: "This stage takes about 10-20 seconds" }
}



const ForgotPasswordVerifyPage = () => {

    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [formError, setFormError] = useState({ password: "", confirmPassword: "" })
    const [isLoading, setIsLoading] = useState(false)
    const [isDone, setIsDone] = useState(false)
    const [status, setStatus] = useState(STATUS.LOADING);
    const query = useQuery();
    const pageRouter = useHistory();
    const oobCode = query.get("oobCode");
    const email = query.get("email");

    if (!oobCode || !email) pageRouter.replace("/");

    const handleChangePassword = (e) => {

        e.preventDefault();

        if (!password) return setFormError({ password: "Password must be atleast 6 characters long!", confirmPassword: "" });
        if (!confirmPassword) return setFormError({ password: "", confirmPassword: "Password must be atleast 6 characters long!" });
        if (password !== confirmPassword) return setFormError({ password: "", confirmPassword: "Password doesn't match!" });

        setFormError({ password: "", confirmPassword: "" });

        setIsLoading(true);

        auth.confirmPasswordReset(oobCode, password).then(result => {
            setStatus(STATUS.DONE);
            setIsDone(true);
        }).catch(err => {
            if ("auth/expired-action-code") {
                setStatus(STATUS.EXPIRED_ACTION_CODE);
                setIsDone(true);
            } else if ("auth/invalid-action-code") {
                setStatus(STATUS.INVALID_ACTION_CODE);
                setIsDone(true);
            } else if ("auth/user-disabled") {
                setStatus(STATUS.USER_DISABLED);
                setIsDone(true);
            } else if ("auth/user-not-found") {
                setStatus(STATUS.USER_NOT_FOUND);
                setIsDone(true);
            } else if ("auth/weak-password") {
                setFormError({ password: "Your password is too weak!" })
            }
        });

    }

    if (isDone)
        return (
            <div>
                <section className="pt-5 px-10 text-center select-none">
                    <div className="max-w-lg shadow-lg mx-auto px-2 py-10 rounded-md bg-white">
                        <h2 className="font-medium text-xl text-red-600">Password Reset Operation</h2>
                        <img className="h-36 w-36 mx-auto my-10" src={status.image} alt={status.title} />
                        <h2 className="font-medium text-lg">{status.title}</h2>
                        <p className="text-xs text-gray-500">{status.subtitle}</p>
                        <div className="px-5 my-5">
                            <Button
                                onClick={_ => { pageRouter.replace("/auth/login") }}
                                isLoading={status.title === STATUS.LOADING.title}
                                className="w-full"
                                value="Continue"
                            />
                        </div>
                    </div>
                </section>
            </div>
        )

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl">Reset Your Password</h2>
                    <img className="w-20 h-20 mx-auto my-5" src={privacyIcon} alt="Password" />
                    <p className="text-sm text-gray-600">You are changing the password for</p>
                    <p className="text-xs text-gray-600 font-semibold">{email}</p>
                    <form onSubmit={handleChangePassword}>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="new-password">Enter New Password</label>
                            <input
                                type="password"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                onChange={({ target: { value } }) => setPassword(value)}
                                value={password}
                                placeholder="Enter New Password"
                                id="new-password" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["password"]}</span>
                        </div>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="confirm-new-password">Confirm New Password</label>
                            <input
                                type="password"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                onChange={({ target: { value } }) => setConfirmPassword(value)}
                                value={confirmPassword}
                                placeholder="Confirm New Password"
                                id="confirm-new-password" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["confirmPassword"]}</span>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <Button isLoading={isLoading} className="w-full" value="Update Password" />
                        </div>
                    </form>
                </div>
            </section>
        </div>
    )

}

export default ForgotPasswordVerifyPage;