import { useState, useEffect } from "react";
import { paginateItems } from "./../../utils/validator";
import Loader from "./../Loader"

const Pagination = ({ list, pageLimit, isLoading, component: Component, notFoundComponent: NotFoundComponent }) => {

    const [currentPage, setCurrentPage] = useState(1);

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [currentPage])

    const { totalItems, totalPages, startIndex, endIndex } = paginateItems(list.length, currentPage, pageLimit);

    const filteredList = list.slice(startIndex, endIndex + 1)

    const handlePrevButton = _ => {
        setCurrentPage(prev => prev === 1 ? 1 : prev - 1)
    }

    const handleNextButton = _ => {
        setCurrentPage(prev => prev + 1)
    }

    if (isLoading)
        return <Loader className="h-52" inline />

    if (filteredList.length === 0)
        return <NotFoundComponent />

    return (
        <>
            {
                filteredList.map((e, i) => <Component key={i} {...e} />)
            }
            <nav className="bg-white py-3 flex items-center justify-between border-t border-gray-200" aria-label="Pagination">
                <div className="hidden sm:block">
                    <p className="text-sm text-gray-700">
                        Showing
                        <span className="font-medium mx-1">{startIndex + 1}</span>
                        to
                        <span className="font-medium mx-1">{endIndex + 1}</span>
                        of
                        <span className="font-medium mx-1">{totalItems}</span>
                        results
                    </p>
                </div>
                <div className="flex-1 flex justify-between sm:justify-end">
                    <button
                        disabled={currentPage === 1}
                        onClick={handlePrevButton}
                        className="relative disabled:cursor-not-allowed disabled:opacity-50 disabled:border-gray-100 inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50"> Previous </button>
                    <button
                        disabled={currentPage === totalPages}
                        onClick={handleNextButton}
                        className="ml-3 relative disabled:cursor-not-allowed disabled:opacity-50 disabled:border-gray-100 inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50"> Next </button>
                </div>
            </nav>
        </>
    );
}

export default Pagination;