import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { addDonorByRequestId } from "./../utils/database";
import RequestsViewDonorsList from './RequestsViewDonorsList';

const FORM_ERROR = { fullName: "", phone: "", units: "" }

const RequestsViewStatusSearch = ({ request, user, isSolved, isUnitsGot, makeToNextSection }) => {

    const [isOpen, setIsOpen] = useState(false);
    const [fullName, setFullName] = useState("");
    const [phone, setPhone] = useState("");
    const [units, setUnits] = useState("");
    const [formError, setFormError] = useState(FORM_ERROR);

    const donorsAdded = request.donors || [];

    const handleSubmitNewDonor = e => {
        e.preventDefault();
        const name = fullName.trim();
        if (!name) return setFormError(prev => ({ ...FORM_ERROR, fullName: "Enter a valid Name!" }));
        if (!phone) return setFormError(prev => ({ ...FORM_ERROR, phone: "Enter a valid Phone!" }));
        if (phone.length !== 10) return setFormError(prev => ({ ...FORM_ERROR, phone: "Enter a valid 10-digit Phone!" }));
        if (!units) return setFormError(prev => ({ ...FORM_ERROR, units: "Enter a valid Name!" }));

        setFormError(FORM_ERROR)

        addDonorByRequestId(request.id, { fullName, phone, units: parseInt(units), addedBy: user.id })
            .then(_ => {
                setFullName("");
                setPhone("");
                setUnits("");
            })
            .catch(_ => {
                setIsOpen(true);
                return setFormError(prev => ({ ...FORM_ERROR, units: "Error in Posting the Donor. Reload the Page!" }));
            })
            .finally(_ => { })
        setIsOpen(false);

    }


    return (
        <main className="py-5">
            <span className="text-sm md:text-base font-medium text-gray-700">Donors found by Community Members</span>
            <RequestsViewDonorsList requestId={request.id} currentUser={user} isSolved={isSolved} donors={donorsAdded} />
            {
                (isUnitsGot && !isSolved) &&
                <div className="w-full text-center py-2 text-gray-600">
                    <h2 className="text-lg">You have got enough donors</h2>
                    <p className="text-sm">You can proceed with next step</p>
                </div>
            }
            {
                !isSolved &&
                <div className="flex flex-col items-center">
                    <button
                        onClick={_ => setIsOpen(true)}
                        className="bg-red-600 shadow-md transform hover:bg-red-700 hover:shadow-lg select-none w-full md:w-2/3 mt-5 active:translate-y-0.5 px-5 py-2 rounded-md text-white">Add New Donor</button>
                    <button
                        onClick={makeToNextSection}
                        className="bg-blue-600 shadow-md transform hover:bg-blue-700 hover:shadow-lg select-none w-full md:w-2/3 mt-5 active:translate-y-0.5 px-5 py-2 rounded-md text-white">Proceed to Next</button>
                </div>
            }
            <Dialog open={isOpen} onClose={_ => setIsOpen(false)} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Add New Donor</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Make sure you have contacted the donor and the donor is available
                    </DialogContentText>
                    <div className="my-2 flex flex-col mx-5 items-start">
                        <label className="text-xs my-2 text-gray-600" htmlFor="name">Full Name</label>
                        <input
                            type="text"
                            className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                            onChange={({ target: { value } }) => setFullName(value)}
                            value={fullName}
                            placeholder="Enter Donor's Full Name"
                            id="name" />
                        <span className="text-xs mt-2 text-red-600 font-medium">{formError["fullName"]}</span>
                    </div>
                    <div className="my-2 flex flex-col mx-5 items-start">
                        <label className="text-xs my-2 text-gray-600" htmlFor="phone">Donor's Phone</label>
                        <input
                            type="number"
                            className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                            onChange={({ target: { value } }) => setPhone(value)}
                            value={phone}
                            placeholder="Enter Donor's Phone"
                            id="phone" />
                        <span className="text-xs mt-2 text-red-600 font-medium">{formError["phone"]}</span>
                    </div>
                    <div className="my-2 flex flex-col mx-5 items-start">
                        <label className="text-xs my-2 text-gray-600" htmlFor="units">Units willing to donate</label>
                        <input
                            type="number"
                            className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                            onChange={({ target: { value } }) => setUnits(value)}
                            value={units}
                            placeholder="Enter Donor's Units"
                            id="units" />
                        <span className="text-xs mt-2 text-red-600 font-medium">{formError["units"]}</span>
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleSubmitNewDonor} color="primary">
                        Add
                    </Button>
                    <Button onClick={_ => setIsOpen(false)} color="secondary">
                        Cancel
                    </Button>
                </DialogActions>
            </Dialog>
        </main>
    );
}

export default RequestsViewStatusSearch;