import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import Logo from "./Logo";
import Button from "./Button";
import { connect } from 'react-redux';
import { auth } from "./../utils/firebase";
import noAvatarIcon from "./../assets/images/no_avatar.png";
import { useTranslation } from 'react-i18next';
import LanguageSelector from './LanguageSelector';
import AppInstallPrompt from './AppInstallPrompt';



const NavBar = ({ isLoggedIn, image }) => {

    const [isOpen, setIsOpen] = useState(false);
    const { t } = useTranslation("Common");

    const isPWA = (navigator.standalone || window.matchMedia("(display-mode: standalone)").matches) || localStorage.getItem("installedPWA");

    useEffect(() => {
        return () => {
            clearTimeout(window.navBarTimeoutID);
        }
    }, [])

    const handleLogout = (_) => {
        auth.signOut()
    }

    const handleOnBlur = (_) => {
        window.navBarTimeoutID = setTimeout(() => {
            setIsOpen(false)
        }, 300);
    }

    if (isLoggedIn)
        return (
            <nav className="py-5 px-5 relative flex items-center select-none  w-full">
                <Link to="/requests"><Logo /></Link>
                {!isPWA && <AppInstallPrompt />}
                <LanguageSelector className={!isPWA ? "mx-3" : "mx-3 ml-auto"} />
                <button
                    onBlur={handleOnBlur}
                    onClick={_ => setIsOpen(prev => !prev)}
                    className="h-10 w-10 border-2 outline-none transform border-red-500 shadow-lg active:translate-y-1 rounded-full cursor-pointer border-dashed overflow-hidden">
                    <img className="object-cover" src={image || noAvatarIcon} alt="Profile" />
                </button>
                {
                    isOpen &&
                    <div className="absolute top-16 mt-2 border-2 border-dashed z-10 shadow-lg rounded-md right-5 bg-white overflow-hidden text-red-600">
                        <Link to="/profile">
                            <div className="hover:bg-gray-300 hover:text-gray-800 cursor-pointer py-3 px-6">{t("Profile")}</div>
                        </Link>
                        <div className="hover:bg-gray-300 hover:text-gray-800 cursor-pointer py-3 px-6" onClick={handleLogout}>{t("Logout")}</div>
                    </div>
                }
            </nav>
        );


    return (
        <nav className="py-5 px-5 flex items-center select-none w-full">
            <Link to="/"><Logo /></Link>
            {!isPWA && <AppInstallPrompt />}
            <LanguageSelector className={!isPWA ? "mx-3" : "mx-3 ml-auto"} />
            <Link to="/auth/login">
                <Button value={t("Login")} className="pt-1 pb-1 h-10" />
            </Link>
        </nav>
    );
}

const mapStateToProps = state => ({
    image: state.auth.image,
    isLoggedIn: state.auth.isLoggedIn,
})

export default connect(mapStateToProps)(NavBar);
