import React, { useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from "./Button"
import { updateRequestById } from "./../utils/database"
import { BLOOD_GROUPS } from "./../utils/config"
import { useTranslation } from "react-i18next"

const FORM_ERROR = { bloodGroup: "", units: "" }

const RequestsViewEdit = ({ className, request }) => {

    const [isOpen, setIsOpen] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [bloodGroup, setBloodGroup] = useState(request.bloodGroup || "");
    const [units, setUnits] = useState(request.units || "");
    const [formError, setFormError] = useState(FORM_ERROR);
    const { t } = useTranslation("Requests");

    const handleUpdateRequest = () => {

        if (!bloodGroup)
            return setFormError({ ...FORM_ERROR, bloodGroup: "Need Patient's Blood Group" })
        if (!BLOOD_GROUPS.includes(bloodGroup) && bloodGroup !== "Any")
            return setFormError({ ...FORM_ERROR, bloodGroup: "Invalid Blood Group" })
        if (!units)
            return setFormError({ ...FORM_ERROR, units: "Need Units between 1 and 50" })

        setIsLoading(true)
        setFormError(FORM_ERROR);

        updateRequestById(request.id, { bloodGroup, units: parseInt(units) })
            .then(_ => {
                setIsLoading(false);
                setIsOpen(false);
            }).catch(_ => {
                setFormError({ ...FORM_ERROR, units: "Something went wrong! Cannot update the request" })
                setIsLoading(false);
            });
    }

    return (
        <>
            <span className={className} onClick={_ => setIsOpen(true)}>{t("Edit")}</span>
            <Dialog fullScreen open={isOpen} onClose={_ => setIsOpen(false)} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">{t("Update Request")}</DialogTitle>
                <DialogContent className="flex flex-col">
                    <DialogContentText>
                        <p className="text-gray-700 font-medium">{t("You can currently change Blood Group and Units required")}.</p>
                        <p className="text-sm">{t("If you need to change the other ones, please delete the request and post a new one")}.</p>
                    </DialogContentText>
                    <div className="flex-1 flex flex-col justify-evenly md:justify-start">
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="blood_group">{t("Required Blood Group")}</label>
                            <select
                                value={bloodGroup}
                                onChange={({ target: { value } }) => setBloodGroup(value)}
                                className="w-full p-2 px-2 text-gray-700 outline-none ring-2 ring-gray-400 focus:ring-red-500 rounded-sm" name="blood_group" id="blood_group">
                                <option value="" disabled={true}>{t("Select Your Blood Group")}</option>
                                <option value="Any" >{t("Any Blood Group")}</option>
                                {
                                    BLOOD_GROUPS.map(e => (
                                        <option key={e} value={e}>{e}</option>
                                    ))
                                }
                            </select>
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["bloodGroup"]}</span>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="units">{t("Required Units")}</label>
                            <input
                                value={units}
                                onChange={({ target: { value } }) => setUnits(value)}
                                type="number"
                                min={1} max={50}
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full" placeholder={t("Enter Required Units")} id="units" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["units"]}</span>
                        </div>
                    </div>
                </DialogContent>
                <DialogActions>
                    <div className="py-3 flex flex-col w-full">
                        <Button isLoading={isLoading} value={t("Update")} className="pt-1 pb-1 md:py-2 mx-2 my-2" onClick={handleUpdateRequest} />
                        <Button value={t("Cancel")} className="pt-1 pb-1 md:py-2 mx-2 my-2" onClick={_ => setIsOpen(false)} />
                    </div>
                </DialogActions>
            </Dialog>
        </>
    );
}

export default RequestsViewEdit;