import { getGender } from "./../utils/validator"

export const getInitialUserData = user => ({
    name: user.displayName,
    image: user.photoURL,
    id: user.uid,
    phone: user.phoneNumber,
    email: user.email,
    emailVerified: user.emailVerified,
})

export const generateRequestID = () => {
    const pattern = [["X", "X", "X"], ["X", "X", "X", "X"], ["X", "X", "X"]];
    return pattern.map(e => e.map(i => Math.floor(Math.random() * 9)).join("")).join("-")
}

export const getRequestModel = data => ({
    ...data,
    reopenedOn: data.reopenedOn?.seconds,
    closedOn: data.closedOn?.seconds,
    needWithin: data.needWithin?.seconds,
    timeStamp: data.timeStamp?.seconds,
})

export const getDonorUpdateModel = user => {
    const { name, phone, id, location, gender, bloodGroup } = user;
    return {
        name,
        phone: parseInt(phone?.replace("+91", "")),
        location,
        gender,
        bloodGroup,
        userId: id,
        isVerified: true,
    }
}

export const getFirstName = (s) => {
    if (!s) return;
    return `${s}`.split(" ")[0];
}

export const getCopyText = ({ requestId, fullName, location, gender, bloodGroup, units, purpose, shortLink }) => {
    return [
        `🃏 Request ID: ${requestId}`,
        `🌟 Name: ${fullName}`,
        `📍 Location: ${location}`,
        `👫 Gender: ${getGender(gender)}`,
        `🩸 Required Blood: ${bloodGroup} [${units} Units]`,
        `❓ Purpose: ${purpose}`,
        `🔗 Track the Request at: ${shortLink}`
    ].join("\n");
}

export const getDonorCopyText = ({ name, location, bloodGroup, gender, phone, userId }) => {
    return [
        `🌟 Name: ${name}`,
        `📍 Location: ${location}`,
        `👫 Gender: ${getGender(gender)}`,
        `🩸 Blood Group: ${bloodGroup}`,
        `📱 Phone: +91${phone}`,
        `🔗 Verified Profile: https://bloodassist.org/users/${userId}`
    ].join("\n")
}

export const getUrgentRequests = (requests = []) => {
    return requests.filter(e => e.status === "UNSOLVED").sort((a, b) => a.needWithin - b.needWithin).slice(0, 6)
}

export const getRecommendedRequests = (requests, location) => {
    return requests
        .filter(e => e.location === location && e.status === "UNSOLVED")
        .sort((a, b) => b.timeStamp - a.timeStamp).slice(0, 12)
}

export const getPersonalRequests = (requests, userId) => {
    return requests
        .filter(e => e.userId === userId)
        .sort((a, b) => b.timeStamp - a.timeStamp).slice(0, 6)
}

export const getRecentlySolvedRequests = (requests) => {
    return requests
        .filter(e => e.status === "SOLVED")
        .sort((a, b) => b.solvedOn - a.solvedOn).slice(0, 12)
}