import logoSvg from "./../assets/images/logo.svg"

const Logo = ({ className }) => {
    return (
        <div className="flex items-center cursor-pointer">
            <img className={className ? "" : "h-8 w-8"} src={logoSvg} alt="Blood Assist" />
            <span className="font-bold ml-2">Blood Assist</span>
        </div>
    );
}

export default Logo;