import React, { useState } from 'react';
import Button from "./../components/Button";
import contactIcon from "./../assets/images/contact.svg";
import flagIcon from "./../assets/images/flag.svg";
import { useHistory } from 'react-router-dom';
import firebase from "./../utils/firebase";

const PhoneVerificationPage = () => {

    const [phoneNumber, setPhoneNumber] = useState("");
    const [formError, setFormError] = useState({ phoneNumber: "" });
    const [isLoading, setIsLoading] = useState(false);
    const pageRouter = useHistory();


    const handleSendOTP = (e) => {
        e.preventDefault();

        if (phoneNumber.length !== 10) return setFormError({ phoneNumber: "Enter a valid 10-digit number!" })

        setFormError({ phoneNumber: "" })
        setIsLoading(true);

        window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container');

        const appVerifier = window.recaptchaVerifier;
        firebase.auth().signInWithPhoneNumber(`+91${phoneNumber}`, appVerifier)
            .then((confirmationResult) => {

                window.confirmationResult = confirmationResult;

                pageRouter.replace("/auth/phone-verification/verify");

            }).catch((error) => {
                console.log(error)
                setIsLoading(false);

            });

    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <form onSubmit={handleSendOTP} className="max-w-md shadow-lg mx-auto px-2 py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl text-red-600">You are one step ahead!</h2>
                    <img className="h-40 mx-auto my-2" src={contactIcon} alt="Phone Number" />
                    <h2 className="font-medium text-lg">Phone Verification</h2>
                    <p className="text-sm text-gray-500">OTP will be sent to the given number</p>
                    <div className="my-3 flex flex-col mx-5 items-start">
                        <label className="text-xs my-2 text-gray-600" htmlFor="phone">Phone</label>
                        <div className="rounded-sm w-full text-sm flex items-center ring-2 ring-gray-400">
                            <img className="h-auto w-6 rounded-sm mx-2" src={flagIcon} alt="India" />
                            <input
                                value={phoneNumber}
                                onChange={({ target: { value } }) => setPhoneNumber(value)}
                                type="number"
                                className="rounded-sm outline-none focus:ring-red-500 flex-1 h-full p-3"
                                placeholder="Enter Your Phone" id="phone" />
                        </div>
                        <span className="text-xs mt-2 text-red-600 font-medium">{formError["phoneNumber"]}</span>
                    </div>
                    <div className="px-5 flex justify-center">
                        <div id="recaptcha-container"></div>
                    </div>
                    {
                        !isLoading &&
                        <div className="px-5">
                            <Button type="submit" className="my-5 w-full" value="Send OTP" />
                        </div>
                    }
                </form>
            </section>
        </div>
    );
}

export default PhoneVerificationPage;