import React, { useEffect, useState } from 'react';
import Toggle from "./../Forms/Toggle";
import { getDonorStatus, updateDonorStatus } from "./../../utils/database";
import { useTranslation } from "react-i18next";

const UpdateStatusNotifier = ({ user, isUpdateRequired }) => {

    const [isDonor, setIsDonor] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const { t } = useTranslation("Donors")

    useEffect(() => {
        if (user && !user.isUpdateRequired)
            getDonorStatus(user)
                .then(e => setIsDonor(e.exists()))
                .catch(_ => { })
    }, [user])

    const handleDonorChange = _ => {
        setIsLoading(true);
        if (isDonor)
            updateDonorStatus(user, true).then(_ => {
                setIsLoading(false);
                setIsDonor(false);
            }).catch(_ => {
                setIsLoading(false);
                setIsDonor(true);
            })
        else
            updateDonorStatus(user, false).then(_ => {
                setIsLoading(false);
                setIsDonor(true);
            }).catch(_ => {
                setIsLoading(false);
                setIsDonor(false);
            })
    }

    if (isUpdateRequired) return <></>

    return (
        <div className="mb-2 mx-5 p-1 px-3 bg-gray-100 text-gray-800 rounded-md flex items-center justify-between flex-wrap">
            <p className="my-1 flex-1 flex flex-col sm:flex-row flex-wrap">
                {
                    isDonor ?
                        "🎉 " + t("Wohooo! You are a Blood Donor now!")
                        : (<>
                            <p className="mr-2">⚡ {t("Are you available for Blood Donation")}?</p>
                            <p>💡 {t("Your current status is Unavailable")}</p>
                        </>)
                }
            </p>
            <Toggle isActive={isDonor} isLoading={isLoading} onClick={handleDonorChange} />
        </div>
    );
}

export default UpdateStatusNotifier;