import noDonorIcon from "./../../assets/images/no_donor.png";

const DonorsNotFound = () => {
    return (
        <div className="flex flex-col items-center w-full py-5">
            <img src={noDonorIcon} alt="No Donors Found" />
            <p className="my-2">No Donors Found</p>
        </div>
    );
}

export default DonorsNotFound;