import React from 'react';
import { getFirstName } from '../utils/generator';
import CompletedStep from "./../components/CompletedStep";
import CurrentStep from "./../components/CurrentStep";

const RequestsViewVerification = ({ isCompleted, setCurrentSelected, verifiedBy }) => {

    if (isCompleted)
        return <CompletedStep
            setCurrentSelected={setCurrentSelected}
            title="Verification"
            description={`${getFirstName(verifiedBy) || "Volunteer"} has verified this request`} />;

    return <CurrentStep setCurrentSelected={setCurrentSelected} stepNo={1} title="Verification" description="Information Collection" />;
}

export default RequestsViewVerification;