import React, { useState, useEffect } from 'react';
import { getShortLink, updateRequestById } from '../utils/database';
import { copyTextForSharing } from '../utils/validator';

const RequestShare = (props) => {

    const [isCopied, setIsCopied] = useState(false);
    const { fullName, bloodGroup, units } = props;

    const { id, shortLink } = props;

    useEffect(() => {

        let timeoutID = 0;

        if (isCopied)
            timeoutID = setTimeout(() => {
                setIsCopied(false);
            }, 2000);

        return () => {
            clearTimeout(timeoutID);
        }

    }, [isCopied])


    if (!shortLink) {
        const title = `Need ${units} Units of ${bloodGroup} Blood for ${fullName} | Blood Assist`
        const desc = `${fullName} needs ${bloodGroup} Blood for the treatment. Help him get a donor.`;
        getShortLink("https://bloodassist.org/requests/" + id, title, desc).then(({ shortLink }) => {
            updateRequestById(id, { shortLink })
        });
    }


    const handleCopy = _ => {
        copyTextForSharing(props);
        setIsCopied(true)
    }

    return (
        <div className="p-1 text-gray-500 relative">
            <button onClick={handleCopy}>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path d="M15 8a3 3 0 10-2.977-2.63l-4.94 2.47a3 3 0 100 4.319l4.94 2.47a3 3 0 10.895-1.789l-4.94-2.47a3.027 3.027 0 000-.74l4.94-2.47C13.456 7.68 14.19 8 15 8z" />
                </svg>
            </button>
            {
                isCopied &&
                <div className="absolute -top-8 -left-5 bg-red-600 px-2 py-1 rounded-sm text-xs text-white">Copied</div>
            }
        </div>
    );
}

export default RequestShare;