import React from 'react';
import CompletedStep from './CompletedStep';
import CurrentStep from './CurrentStep';
import UpcomingStep from './UpcomingStep';


const RequestViewFinal = ({ isCurrent, isCompleted, setCurrentSelected, disabled }) => {

    if (isCompleted)
        return <CompletedStep setCurrentSelected={setCurrentSelected} title="Connect Donor" description="Closing the Request" />;

    if (isCurrent)
        return <CurrentStep setCurrentSelected={setCurrentSelected} stepNo={3} title="Connect Donor" description="Closing the Request" />;

    return <UpcomingStep disabled={disabled} setCurrentSelected={setCurrentSelected} stepNo={3} title="Connect Donor" description="Closing the Request" />;

}

export default RequestViewFinal;