import html2canvas from "html2canvas";
import { useRef, useState } from "react";
import SelectImagePromo from "../../components/Forms/SelectImagePromo";
import { PROMO_BLOOD_GROUPS } from "../../utils/config";
import Button from "./../../components/Button";
import { Link } from "react-router-dom";
import { downloadFromLink } from "./../../utils/validator";

const IAmThereCampaign = () => {

    const [fileUrl, setFileUrl] = useState("");
    const [bloodGroup, setBloodGroup] = useState("");
    const [isLoading, setIsLoading] = useState(false);
    const imageRef = useRef(null);

    const handleGenerateImage = _ => {

        if (!fileUrl)
            return alert("Select a Picture!");
        if (!bloodGroup)
            return alert("Select a Blood Group!");
        if (!PROMO_BLOOD_GROUPS.includes(bloodGroup))
            return alert("Select a valid Blood Group!");

        setIsLoading(true);
        if (imageRef.current) {
            setTimeout(async () => {
                const result = await html2canvas(imageRef.current)
                downloadFromLink(result.toDataURL(), "IAmThereCampaignDP");
                setIsLoading(false);
            }, 100)
        } else {
            setIsLoading(false);
        }
    }

    return (
        <div className="bg-white max-w-6xl mx-auto rounded-lg shadow-lg overflow-hidden">
            <div className="mx-auto py-10 px-4 sm:px-6 md:py-16 lg:px-8 lg:py-14">
                <p className="text-2xl font-extrabold tracking-tight flex items-center flex-wrap text-gray-900 sm:text-3xl">
                    <span className="font-semibold text-gray-700 mr-2">{("Join the Cause")}</span>
                    <span className="text-4xl text-red-600">{("#IAmThere")}</span>
                </p>
                <p className="my-2">
                    The main motto of this campaign is to show that you are available for your loved ones.
                </p>
                <div className="my-2">
                    <h1 className="font-semibold">What should I do?</h1>
                    <p>Take a small step to include your Blood Group in your public profiles. You can also use below generator to add badge to your Display Picture (DP).</p>
                </div>
                <div className="my-2">
                    <h1 className="font-semibold">Let's Spread Love</h1>
                    <div className="flex items-center justify-around my-4 flex-wrap">
                        <div className="flex flex-col items-center">
                            <SelectImagePromo
                                bloodGroup={bloodGroup}
                                isGenerate={isLoading}
                                fileUrl={fileUrl}
                                setFileUrl={setFileUrl}
                                imageRef={imageRef} />
                            <h2 className="font-medium text-sm my-1">Your Picture</h2>
                        </div>
                        <div className="flex flex-col justify-center items-stretch">
                            <h2 className="font-medium text-sm">Your Blood Group</h2>
                            <select
                                value={bloodGroup}
                                onChange={({ target: { value } }) => setBloodGroup(value)}
                                className="w-full p-2 px-2 my-2 text-gray-700 outline-none ring-2 ring-gray-400 focus:ring-red-500 rounded-sm" name="blood_group" id="blood_group">
                                <option value="" disabled={true}>{("Select Your Blood Group")}</option>
                                {
                                    PROMO_BLOOD_GROUPS.map(e => (
                                        <option key={e} value={e}>{e}</option>
                                    ))
                                }
                            </select>
                            <Button onClick={handleGenerateImage} isLoading={isLoading} value="Generate" className="my-5" />
                        </div>
                    </div>
                </div>
                <div className="my-2">
                    <h1 className="font-semibold">What's the use?</h1>
                    <p>If someone needs Blood during emergency, this badge will help them to find / know your Blood Group.</p>
                </div>
                <div className="my-2">
                    <h1 className="font-semibold">What's more?</h1>
                    <p>If you are willing to be a Blood Donor / Volunteer, please do register <Link className="font-medium text-indigo-800" to="/auth/register">here</Link></p>
                </div>
                <div className="mt-5 text-sm">
                    <h1 className="font-semibold">Privacy Note:</h1>
                    <p>Your image is neither stored nor sent to the server for creating badge.</p>
                    <p>We are team of volunteers and this platform runs on Public Funding. So, don't have server to store stuffs.</p>
                    <p>Don't worry about the privacy :) !</p>
                </div>
            </div>
        </div>
    );
}

export default IAmThereCampaign;