import React, { useState } from 'react';
import Button from '../components/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useTranslation } from "react-i18next";

const DonatePage = () => {

    const [isDonateOpen, setIsDonateOpen] = useState(false);
    const [isFeedbackOpen, setIsFeedbackOpen] = useState(false);
    const { t } = useTranslation("About");

    return (
        <div className="bg-white max-w-6xl mx-auto rounded-lg shadow-lg">
            <div className="max-w-6xl mx-auto py-12 px-4 sm:px-6 md:py-16 lg:px-8 lg:py-20">
                <h2 className="text-3xl font-extrabold tracking-tight text-gray-900 sm:text-4xl">
                    <span className="block font-semibold text-gray-700">{t("Loving our service")}?</span>
                    <span className="block text-red-600 mt-5">{t("Help us maintain Blood Assist")}</span>
                </h2>
                <div className="mt-8 flex flex-wrap">
                    <div className="m-1 inline-flex rounded-md shadow">
                        <Button onClick={_ => setIsDonateOpen(true)} value={t("Donate")} className="px-5 py-3" />
                    </div>
                    <div className="m-1 inline-flex">
                        <button onClick={_ => setIsFeedbackOpen(true)} className="inline-flex outline-none transform active:translate-y-0.5 select-none items-center justify-center px-5 py-3 border border-transparent text-base font-medium rounded-md text-red-700 bg-indigo-100 hover:bg-indigo-200"> {t("Say feedback")}! </button>
                    </div>
                </div>
            </div>

            <Dialog
                open={isDonateOpen}
                onClose={_ => setIsDonateOpen(false)}
                aria-labelledby="alert-dialog-title-1"
                aria-describedby="alert-dialog-description-2"
            >
                <DialogTitle id="alert-dialog-title-1">{t("Donation")}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description-2">
                        <p className="text-xl text-gray-700 font-medium">{t("Bank Account Details")}</p>
                        <ul className="my-5">
                            <li><h3 className="font-medium text-gray-600 inline-block">Acc. Name:</h3> SHRI HARI L</li>
                            <li><h3 className="font-medium text-gray-600 inline-block">Acc. Num:</h3> 003301530298</li>
                            <li><h3 className="font-medium text-gray-600 inline-block">Bank:</h3> ICICI Bank</li>
                            <li><h3 className="font-medium text-gray-600 inline-block">IFSC Code:</h3> ICIC0000033</li>
                            <li><h3 className="font-medium text-gray-600 inline-block">Branch:</h3> Pricol Branch, Perianaickenpalayam, Coimbatore, Tamil Nadu</li>
                        </ul>
                        <p><h3 className="font-medium text-gray-600 inline-block">G-Pay:</h3> +91 94873 49310</p>
                        <p><h3 className="font-medium text-gray-600 inline-block">PhonePe:</h3> +91 94873 49310</p>
                        <p><h3 className="font-medium text-gray-600 inline-block">UPI ID:</h3> djnitharshan@okaxis</p>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <div className="w-full flex-justify-center">
                        <Button value={t("Sure, I will")} className="w-full" onClick={_ => setIsDonateOpen(false)} />
                    </div>
                </DialogActions>
            </Dialog>


            <Dialog
                open={isFeedbackOpen}
                onClose={_ => setIsFeedbackOpen(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{t("Want to share your thoughts with us")}?</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <p>{t("Thanks for the interest")}! 🎉</p>
                        <p>{t("Do fill out this form")}</p>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <a href="https://forms.gle/ghg3jRrhErwhgCAU6" target="_blank" rel="noreferrer" className="w-full flex-justify-center">
                        <Button value={t("Fill this form")} className="w-full" onClick={_ => setIsFeedbackOpen(false)} />
                    </a>
                </DialogActions>
            </Dialog>

        </div>
    );
}

export default DonatePage;