import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import RequestsUrgent from "./../components/RequestsUrgent";
import RequestsRecommended from "./../components/RequestsRecommended";
import RequestsPersonal from "./../components/RequestsPersonal";
import RequestsRecentlySolved from "./../components/RequestsRecentlySolved";
import { getAllRecentlySolvedRequests, getAllRequests, getAllUserRequests } from "./../utils/database"
import { setRequests } from "./../cache/request"
import { getRequestModel } from '../utils/generator';
import { Link, useHistory } from 'react-router-dom';
import Button from './../components/Button';
import UpdateProfileNotifier from './../components/Popups/UpdateProfileNotifier';
import noRequestsIcon from "./../assets/images/no_requests.svg";
import { useTranslation } from "react-i18next";

const RequestsPage = ({ requests, setRequests, user }) => {

    const [isLoading, setIsLoading] = useState(true);
    const { t } = useTranslation("Requests")
    const pageRouter = useHistory();
    const userId = user.id;

    useEffect(() => {

        Promise.all([getAllRequests(), getAllUserRequests(userId), getAllRecentlySolvedRequests()])
            .then(results => {
                const requests = results.reduce((acc, curr) => acc.concat(curr.docs.map(e => getRequestModel(e.data()))), [])
                setRequests(requests);
                setIsLoading(false);
            }).catch(_ => { console.log(_); pageRouter.replace("/something-went-wrong") })

    }, [setRequests, pageRouter, userId])

    if (!isLoading && requests.length === 0)
        return (
            <>
                <UpdateProfileNotifier />
                <div className="no-requests-container flex flex-col items-center justify-center">
                    <img className="h-1/2" src={noRequestsIcon} alt="No Requests" />
                    <p className="text-lg text-gray-800 font-semibold mt-5">No Active Blood Requests</p>
                    <p className="text-gray-600 text-center">Huraayy! We don't have any requests to solve</p>
                    <Link to="/requests/new" className="mt-5">
                        <Button value="New Request" className="m-0 rounded-sm" />
                    </Link>
                </div>
            </>
        )

    return (
        <>
            <UpdateProfileNotifier />
            {
                !isLoading &&
                <Link to="/requests/new" className="absolute right-3 m-2">
                    <Button value={t("New Request")} className="m-0 pt-1 pl-2 pr-2 pb-1 rounded-sm" />
                </Link>
            }
            <RequestsUrgent requests={requests} isLoading={isLoading} />
            <RequestsPersonal userId={user.id} requests={requests} isLoading={isLoading} />
            <RequestsRecommended location={user.location} requests={requests} isLoading={isLoading} />
            <RequestsRecentlySolved requests={requests} isLoading={isLoading} />
        </>
    );
}

const mapStateToProps = state => ({
    user: state.auth,
    requests: Object.keys(state.requests).map(e => state.requests[e])
})

const mapDispatchToProps = dispatch => ({
    setRequests: requests => dispatch(setRequests(requests))
})

export default connect(mapStateToProps, mapDispatchToProps)(RequestsPage);