import {
    BrowserRouter as Router,
    Switch,
    Redirect,
    Route
} from "react-router-dom";
import HomePage from "../pages/HomePage";
import LoginPage from "../pages/LoginPage";
import RegisterPage from "../pages/RegisterPage";
import EmailVerificationVerifyPage from "../pages/EmailVerificationVerifyPage";
import SomethingWentWrongPage from "../pages/SomethingWentWrongPage";
import ForgotPasswordPage from "../pages/ForgotPasswordPage";
import ForgotPasswordVerifyPage from "../pages/ForgotPasswordVerifyPage";
import NavBar from "../components/NavBar";
import FooterPublic from "../components/FooterPublic";
import RequestsViewPublicPage from "../pages/RequestsViewPublicPage";
import AboutPage from "../pages/AboutPage";
import DonatePage from "../pages/DonatePage";
import DonorSearchView from "../pages/DonorSearchView";
import DonorSearchPage from "../pages/DonorSearchPage";
import ScrollToTop from "../components/ScrollToTop";
import IAmThereCampaignPage from "../pages/Promos/IAmThere";

const PublicRoutes = () => {
    return (
        <Router>
            <NavBar />
            <ScrollToTop />
            <Switch>
                <Route exact path="/" component={HomePage} />
                <Route exact path="/auth/login" component={LoginPage} />
                <Route exact path="/auth/register" component={RegisterPage} />
                <Route exact path="/auth/forgot-password/reset" component={ForgotPasswordVerifyPage} />
                <Route exact path="/auth/forgot-password" component={ForgotPasswordPage} />
                <Route exact path="/auth/email-verification/verify" component={EmailVerificationVerifyPage} />
                <Route exact path="/something-went-wrong" component={SomethingWentWrongPage} />
                <Route exact path="/iamthere" component={IAmThereCampaignPage} />
                <Route exact path="/donors/search" component={DonorSearchPage} />
                <Route exact path="/donors/:place" component={DonorSearchView} />
                <Redirect exact from="/donors" to="/donors/search" />
                <Route exact path="/about" component={AboutPage} />
                <Route exact path="/donate" component={DonatePage} />
                <Route exact path="/team" component={SomethingWentWrongPage} />
                <Redirect from="/requests/new" to="/" exact />
                <Route exact path="/requests/:requestId" component={RequestsViewPublicPage} />
                <Redirect from="/r/:id" to="/requests/:id" exact />
                <Redirect from="/d/:place" to="/donors/:place" exact />
                <Redirect to={{ pathname: "/auth/login", state: { message: "Security Alert: Login to Continue" } }} exact />
            </Switch>
            <FooterPublic />
        </Router>
    )
}

export default PublicRoutes;