import React from 'react';
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import UpdateStatusNotifier from "./UpdateStatusNotifier"

const UpdateProfileNotifier = ({ user, isUpdateRequired }) => {
    if (isUpdateRequired === null) return <></>

    if (isUpdateRequired === true)
        return (
            <div className="mb-2 mx-5 p-1 px-3 bg-red-600 text-gray-200 rounded-md flex items-center justify-between flex-wrap">
                <p className="font-light text-sm my-1">🔔 You haven't completed your profile. </p>
                <Link to="/profile/update" className="my-1">
                    <span className="font-medium text-sm bg-white p-2 py-1 rounded-md text-gray-600">Update Now</span>
                </Link>
            </div>
        );

    if (user && isUpdateRequired === false)
        return <UpdateStatusNotifier user={user} isUpdateRequired={isUpdateRequired} />

    return <></>
}

const mapStateToProps = state => ({
    user: state.auth,
    isUpdateRequired: state.auth.isUpdateRequired
})

export default connect(mapStateToProps)(UpdateProfileNotifier);