import React, { useState } from "react";
import { Link } from "react-router-dom";
import Button from "../components/Button";
import GoogleSignInButton from "../components/GoogleSignInButton";
import { auth } from "./../utils/firebase";
import { useTranslation } from "react-i18next"

const ERRORS = {
    UNKNOWN: "Error in Creating the User!",
    INVALID_MAIL: "The Email is invalid!",
    WEAK_PASSWORD: "The Password is weak! Make it stronger!",
    EMAIL_ALREADY_IN_USE: "The Email is already registered!",
    NO_ERROR: ""
}

const FORM_ERROR = { fullName: "", email: "", password: "" }

const RegisterPage = () => {

    const [fullName, setFullName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState(ERRORS.NO_ERROR);
    const [formError, setFormError] = useState(FORM_ERROR);
    const [isLoading, setIsLoading] = useState(false);
    const { t } = useTranslation("Auth");

    const handleRegisterUser = (e) => {
        e.preventDefault();
        const name = fullName.trim();
        if (!name) return setFormError(_ => ({ ...FORM_ERROR, fullName: "Enter a valid Name!" }));
        if (!email) return setFormError(_ => ({ ...FORM_ERROR, email: "Enter a valid Email!" }));
        if (!password) return setFormError(_ => ({ ...FORM_ERROR, password: "Enter a valid Password!" }));
        if (password.length < 8) return setFormError(_ => ({ ...FORM_ERROR, password: "Password should be atleast 8 characters!" }));

        setFormError(FORM_ERROR)

        setIsLoading(true);
        auth.createUserWithEmailAndPassword(email, password).then(({ user }) => {
            user.updateProfile({ displayName: name })
            user.sendEmailVerification();
        }).catch(({ code }) => {
            if (code === "auth/email-already-in-use")
                return setError(ERRORS.EMAIL_ALREADY_IN_USE)
            if (code === "auth/invalid-email")
                return setError(ERRORS.INVALID_MAIL)
            if (code === "auth/weak-password")
                return setError(ERRORS.WEAK_PASSWORD)

            return setError(ERRORS.UNKNOWN)
        }).finally(() => {
            setIsLoading(false);
        });

    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl">{t("Hi, there")}! {t("Register in Blood Assist")}!</h2>
                    <p className="text-xs text-gray-600 my-1">{t("Currently we are allowing only Tamil Nadu users")}</p>
                    {error && <div className="p-2 bg-red-100 mx-5 my-2 text-gray-800 rounded-sm">{error}</div>}
                    <form onSubmit={handleRegisterUser}>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="name">{t("Full Name")}</label>
                            <input
                                type="text"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                onChange={({ target: { value } }) => setFullName(value)}
                                value={fullName}
                                placeholder={t("Enter Your Full Name")}
                                id="name" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["fullName"]}</span>
                        </div>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="email">{t("Email Address")}</label>
                            <input
                                type="email"
                                onChange={({ target: { value } }) => setEmail(value)}
                                value={email}
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                placeholder={t("Enter Your Email")}
                                id="email" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["email"]}</span>
                        </div>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="password">{t("Password")}</label>
                            <input
                                onChange={({ target: { value } }) => setPassword(value)}
                                value={password}
                                type="password"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                placeholder={t("Enter Password")}
                                id="password" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["password"]}</span>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <Button isLoading={isLoading} className="w-full" value={t("Register")} />
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <GoogleSignInButton />
                        </div>
                        <div className="flex flex-wrap justify-center items-center mt-5">
                            <div className="mr-1 text-sm">{t("Already on Blood Assist")} ?</div>
                            <Link to="/auth/login" className="font-medium text-red-600">
                                {t("Login")}
                            </Link>
                        </div>
                    </form>
                </div>
            </section >
        </div >
    );
}

export default RegisterPage;