import React, { useState, useEffect } from 'react';
import { copyDonorForSharing } from '../../utils/validator';

const DonorSearch = ({ donor }) => {

    const [isCopied, setIsCopied] = useState(false);

    useEffect(() => {
        let timeoutID = 0;
        if (isCopied)
            timeoutID = setTimeout(() => {
                setIsCopied(false);
            }, 2000);
        return () => {
            clearTimeout(timeoutID);
        }
    }, [isCopied])

    const handleCopy = _ => {
        copyDonorForSharing(donor);
        setIsCopied(true)
    }

    return (
        <button onClick={handleCopy} className="p-1.5 px-4 flex-1 bg-indigo-500 hover:bg-indigo-700 transform active:translate-y-0.5 justify-center rounded-md font-medium text-white cursor-pointer flex items-center gap-2">
            <span>
                {
                    isCopied ?
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                        </svg>
                        :
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path d="M15 8a3 3 0 10-2.977-2.63l-4.94 2.47a3 3 0 100 4.319l4.94 2.47a3 3 0 10.895-1.789l-4.94-2.47a3.027 3.027 0 000-.74l4.94-2.47C13.456 7.68 14.19 8 15 8z" />
                        </svg>
                }
            </span>
            <span>{isCopied ? "Copied" : "Share"}</span>
        </button>
    );
}

export default DonorSearch;