import React, { useState } from 'react';
import connectIcon from "./../assets/images/connect.jpg"
import Button from "./Button";
import noAvatarIcon from "./../assets/images/no_avatar.png";
import { updateRequestById } from "./../utils/database";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { timeStampValue } from "./../utils/firebase";

const RequestsViewStatusDone = ({ request, user, isSolved }) => {

    const [isOpen, setIsOpen] = useState(false);

    const handleCloseRequest = () => {
        updateRequestById(request.id, { status: "SOLVED", closedBy: user, closedOn: timeStampValue() });
        setIsOpen(false);
    }

    const handleReopenRequest = () => {
        updateRequestById(request.id, { status: "UNSOLVED", reopenedBy: user, reopenedOn: timeStampValue() });
        setIsOpen(false);
    }


    if (isSolved)
        return (
            <main className="py-5 flex items-center flex-col-reverse md:flex-row">
                <div className="flex flex-col items-center md:w-1/2 text-gray-700">
                    <p className="text-lg font-medium">Request Solved</p>
                    <p className="text-sm mt-2 mb-5">Thank You everyone who were part of this!</p>
                    <Button onClick={_ => setIsOpen(true)} value="Reopen the Request" />
                    <div className="flex items-center my-5 justify-center w-full">
                        <label className="text-xs md:text-sm text-gray-600" htmlFor="closedBy">Closed by</label>
                        <a href={`/users/${request.closedBy?.id}`} target="_blank" rel="noreferrer" className="flex items-center ml-2">
                            <img className="border-2 border-red-700 border-dashed h-8 w-8 rounded-full shadow-lg" src={request.closedBy?.image || noAvatarIcon} alt="Profile" />
                            <div className="ml-2 text-indigo-900">{request.closedBy?.name}</div>
                        </a>
                    </div>
                </div>

                <div className="flex justify-center md:w-1/2 md:border-l-2">
                    <img className="h-64" src={connectIcon} alt="Solved" />
                </div>

                <Dialog
                    open={isOpen}
                    onClose={_ => setIsOpen(false)}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">Are you sure about this?</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            Reopen the request only if you think the patient still needs the help
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleReopenRequest} value="Yes" className="pt-1 pb-1" />
                        <Button onClick={_ => setIsOpen(false)} value="Cancel" className="pt-1 pb-1" />
                    </DialogActions>
                </Dialog>
            </main>
        )

    return (
        <main className="py-5 flex items-center flex-col-reverse md:flex-row">

            <div className="flex flex-col items-center md:w-1/2 text-gray-700">
                <p className="text-lg font-medium">Connect the Donors to the Patient</p>
                <p className="text-sm mt-2 mb-5">Once everything is done, mark it as completed</p>
                <Button onClick={_ => setIsOpen(true)} value="Close the Request" />
                {
                    request.reopenedBy?.id &&
                    <div className="flex items-center my-5 justify-center w-full">
                        <label className="text-xs md:text-sm text-gray-600" htmlFor="closedBy">Reopened by</label>
                        <a href={`/users/${request.reopenedBy?.id}`} target="_blank" rel="noreferrer" className="flex items-center ml-2">
                            <img className="border-2 border-red-700 border-dashed h-8 w-8 rounded-full shadow-lg" src={request.reopenedBy?.image || noAvatarIcon} alt="Profile" />
                            <div className="ml-2 text-indigo-900">{request.reopenedBy?.name}</div>
                        </a>
                    </div>
                }
            </div>

            <div className="flex justify-center md:w-1/2 md:border-l-2">
                <img className="h-64" src={connectIcon} alt="Solved" />
            </div>

            <Dialog
                open={isOpen}
                onClose={_ => setIsOpen(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">Are you sure about this?</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Thanks for volunteering. You are about to close the request.<br /> Do perform this when you know that patient got the blood.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseRequest} value="Yes" className="pt-1 pb-1" />
                    <Button onClick={_ => setIsOpen(false)} value="No" className="pt-1 pb-1" />
                </DialogActions>
            </Dialog>
        </main >
    );
}

export default RequestsViewStatusDone;