import React from 'react';
import RequestsUrgentLoader from "./Loader/RequestsUrgentLoader";
import { isEmpty } from "./../utils/validator";
import RequestCard from './RequestCard';
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { getRecentlySolvedRequests } from '../utils/generator';

const RequestsRecentlySolved = ({ isLoading, requests }) => {

    const { t } = useTranslation("Requests")
    const recentFilter = getRecentlySolvedRequests(requests);

    if (!isLoading && isEmpty(recentFilter)) return <></>;

    return (
        <section className="p-5 select-none">
            <h1 className="font-semibold mb-5 text-gray-600">{t("Recently Solved")}</h1>
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-3">
                {isLoading ?
                    <RequestsUrgentLoader />
                    : recentFilter.map((e, i) => <Link key={i} to={`/requests/${e.id}`}><RequestCard request={e} /></Link>)
                }
            </div>
        </section>
    );
}

export default RequestsRecentlySolved;