import firebase from 'firebase/app';
import { FirebaseDynamicLinks } from 'firebase-dynamic-links';
import 'firebase/auth';
import 'firebase/database';
import 'firebase/firestore';

if (firebase.apps.length === 0)
    firebase.initializeApp({
        apiKey: process.env.REACT_APP_API_KEY,
        authDomain: process.env.REACT_APP_AUTH_DOMAIN,
        databaseURL: process.env.REACT_APP_DATABASE_URL,
        projectId: process.env.REACT_APP_PROJECT_ID,
        storageBucket: process.env.REACT_APP_STORAGE_BUCKET,
        messagingSenderId: process.env.REACT_APP_MESSAGING_ID,
        appId: process.env.REACT_APP_APP_ID,
        measurementId: process.env.REACT_APP_MEASUREMENT_ID,
    })

export const auth = firebase.auth();
export const database = firebase.database();
export const firestore = firebase.firestore();
export const dynamicLink = new FirebaseDynamicLinks(process.env.REACT_APP_API_KEY)
export const timeStampValue = firebase.firestore.FieldValue.serverTimestamp
export const databaseTimestampValue = firebase.database.ServerValue.TIMESTAMP

export default firebase;