import React from 'react';
import Quote from "../components/Quote";
import Button from "../components/Button";
import OutlineButton from "../components/OutlineButton";
import { Link } from "react-router-dom";
import forwardIcon from "./../assets/images/forward.svg";
import collaborateIcon from "./../assets/images/collaborate.svg";
import organizedIcon from "./../assets/images/be_organized.svg";
import introIcon from "./../assets/images/intro.png";
import { useTranslation } from 'react-i18next';

const HomePage = () => {

    const { t } = useTranslation("Home");

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="sm:flex items-center flex-row-reverse justify-around">
                    <img className="w-3/4 md:w-3/5 sm:h-auto sm:w-5/6 max-w-xs mx-auto md:ml-auto md:mr-5" src={introIcon} alt="Intro" />
                    <div className="flex flex-col items-center justify-center mx-auto">
                        <blockquote className="mt-5 flex justify-center">
                            <div className="max-w-3xl mx-auto text-xl sm:text-2xl sm:leading-9 leading-7 font-medium text-gray-900">
                                <p className="flex items-center">
                                    <span className="text-6xl mr-2 text-red-600">“</span>
                                    <Quote />
                                    <span className="text-6xl ml-2 text-red-600">”</span>
                                </p>
                            </div>
                        </blockquote>
                        <div className="flex items-center flex-wrap justify-center">
                            <Link to="/auth/login">
                                <Button value={t("Get Started")} className="sm:mx-5 mx-2 mt-5 " />
                            </Link>
                            <Link to="/about">
                                <OutlineButton value={t("Know More")} className="sm:mx-5 mx-2 mt-5 " />
                            </Link>
                        </div>
                    </div>
                </div>
            </section>
            <section className="mt-5 px-5 py-2 select-none">
                <h3 className="font-medium text-gray-800 text-xl">{t("How Blood Assist Works?")}</h3>
                <div className="grid sm:grid-cols-3 sm:gap-x-5 grid-cols-1 gap-y-2 mt-4">
                    <div className="group my-1 hover:bg-red-600 hover:border-b-2 border-red-900 hover:text-white transition-colors ease-in-out duration-200 flex flex-col relative h-40 p-3 items-center text-center justify-center bg-white shadow-lg rounded-md">
                        <h2 className="font-medium md:text-lg">{t("Post Your Request")}</h2>
                        <span className="text-center text-sm md:text-base mt-2 px-2">{t("Create a request with complete details of the required blood")}</span>
                        <div className="absolute top-0 right-0 opacity-10">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 group-hover:fill-current group-hover:text-white" viewBox="0 0 20 20" fill="currentColor">
                                <path d="M17.414 2.586a2 2 0 00-2.828 0L7 10.172V13h2.828l7.586-7.586a2 2 0 000-2.828z" />
                                <path fillRule="evenodd" d="M2 6a2 2 0 012-2h4a1 1 0 010 2H4v10h10v-4a1 1 0 112 0v4a2 2 0 01-2 2H4a2 2 0 01-2-2V6z" clipRule="evenodd" />
                            </svg>
                        </div>
                    </div>
                    <div className="group my-1 hover:bg-red-600 hover:border-b-2 border-red-900 hover:text-white transition-colors ease-in-out duration-200 flex flex-col relative h-40 px-3 items-center text-center justify-center bg-white shadow-lg rounded-md">
                        <h2 className="font-medium md:text-lg">{t("Keep Track of your Request")}</h2>
                        <span className="text-center text-sm md:text-base mt-2 px-2">{t("Let Volunteer Community searches for the Blood")}</span>
                        <div className="absolute top-0 right-0 opacity-10">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 group-hover:fill-current group-hover:text-white" viewBox="0 0 20 20" fill="currentColor">
                                <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd" />
                            </svg>
                        </div>
                    </div>
                    <div className="group my-1 hover:bg-red-600 hover:border-b-2 border-red-900 hover:text-white transition-colors ease-in-out duration-200 flex flex-col relative h-40 px-3 items-center text-center justify-center bg-white shadow-lg rounded-md">
                        <h2 className="font-medium md:text-lg">{t("Get connected with Donor")}</h2>
                        <span className="text-center text-sm md:text-base mt-2 px-2">{t("Once donor found, get connect with them for further")}!</span>
                        <div className="absolute top-0 right-0 opacity-10">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 group-hover:fill-current group-hover:text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 21v-4m0 0V5a2 2 0 012-2h6.5l1 1H21l-3 6 3 6h-8.5l-1-1H5a2 2 0 00-2 2zm9-13.5V9" />
                            </svg>
                        </div>
                    </div>
                </div>
            </section>
            <section id="why-blood-assist" className="mt-5 px-5 py-2 select-none">
                <h3 className="font-medium text-gray-800 text-xl">{t("Why Blood Assist")}?</h3>
                <div className="flex md:flex-row flex-col items-center justify-center  my-5">
                    <img className="h-40 w-40" src={forwardIcon} alt="Forward Message" />
                    <div className="flex flex-col items-center bg-white py-5 px-3 mx-3 md:mx-6 rounded-xl shadow-lg">
                        <h2 className="font-medium my-1">{t("Avoid Forward Messages")}</h2>
                        <p className="text-center text-sm md:text-base text-gray-600 px-5">{t("Lot of Blood Requests are being forwarded even after they gets donor")}</p>
                    </div>
                </div>
                <div className="flex md:flex-row-reverse flex-col items-center justify-center  my-5">
                    <img className="h-40 w-40" src={collaborateIcon} alt="Find Collaboratively" />
                    <div className="flex flex-col items-center bg-white py-5 px-3 rounded-xl mx-3 md:mx-6 shadow-lg">
                        <h2 className="font-medium my-1">{t("Find Collaboratively")}</h2>
                        <p className="text-center text-sm md:text-base text-gray-600 px-5">{t("Volunteers can now search collaboratively and avoid duplicating the work of others")}</p>
                    </div>
                </div>
                <div className="flex md:flex-row flex-col items-center justify-center  my-5">
                    <img className="h-40 w-40" src={organizedIcon} alt="Organized" />
                    <div className="flex flex-col items-center bg-white py-5 px-3 mx-3 md:mx-6 rounded-xl shadow-lg">
                        <h2 className="font-medium my-1">{t("Be Organized")}</h2>
                        <p className="text-center text-sm md:text-base text-gray-600 px-5">{t("Get Right Information about the Patient before searching for the donor")}</p>
                    </div>
                </div>
            </section>
        </div>
    );
}

export default HomePage;