import React, { useState } from 'react';
import Button from "./../components/Button"
import mailBoxIcon from "./../assets/images/mailbox.png";
import { auth } from "./../utils/firebase";

const EmailVerificationPage = () => {

    const [isLoading, setIsLoading] = useState(false);
    const [isSent, setIsSent] = useState(false);

    const handleResendMail = () => {
        setIsLoading(true);
        const user = auth.currentUser
        if (user)
            user.sendEmailVerification()
                .then(_ => setIsSent(true))
                .catch(_ => { })
                .finally(_ => setIsLoading(false))
    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-lg shadow-lg mx-auto px-2 py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl text-red-600">Welcome to Blood Assist!</h2>
                    <img className="h-44 w-44 mx-auto my-5 rounded-full" src={mailBoxIcon} alt="Email Sent" />
                    <h2 className="font-medium text-lg">Email Verification</h2>
                    <p className="text-gray-500">Check your Spam Folder if you didn't receive the mail</p>
                    <div className="px-5 my-5">
                        {isSent ?
                            <Button disabled className="w-full" value="Mail Sent!" />
                            : <Button isLoading={isLoading} className="w-full" onClick={handleResendMail} value="Resend Verification Mail" />
                        }
                    </div>
                </div>
            </section>
        </div>
    );
}

export default EmailVerificationPage;