import React from 'react';
import RequestsUrgentLoader from "./Loader/RequestsUrgentLoader";
import { isEmpty } from "./../utils/validator";
import RequestCard from './RequestCard';
import { Link } from "react-router-dom";
import { getPersonalRequests } from '../utils/generator';
import { useTranslation } from 'react-i18next';

const RequestsPersonal = ({ isLoading, requests, userId }) => {

    const { t } = useTranslation("Requests")
    const personalFilter = getPersonalRequests(requests, userId);

    if (!isLoading && isEmpty(personalFilter)) return <></>;

    return (
        <section className="p-5 select-none">
            <h1 className="font-semibold mb-5 text-gray-600">{t("My Requests")}</h1>
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-3">
                {isLoading ?
                    <RequestsUrgentLoader />
                    : personalFilter.map((e, i) => <Link key={i} to={`/requests/${e.id}`}><RequestCard request={e} /></Link>)
                }
            </div>
        </section>
    );
}

export default RequestsPersonal;