import { useTranslation } from "react-i18next";

const QUOTES = [
    "Your droplets of blood may create an ocean of happiness",
    "Donate blood and be the reason of smile to many faces",
    "A few drops of your blood can help a life to bloom",
    "Blood can circulate forever if you keep donating it",
    "The blood is red gold in time of saving a life"
]

const Quote = () => {

    const { t } = useTranslation("Home")
    const quote = QUOTES[Math.floor(Math.random() * QUOTES.length)];

    return (
        <span className="blood-quote">
            {t(quote)}
            <span className="mx-1 text-3xl animate-pulse duration-100 text-red-600 font-black">|</span>
        </span>
    );
}

export default Quote;