import { useRef } from "react";
import Loader from "./../Loader"

const SelectImagePromo = ({ fileUrl, setFileUrl, imageRef, isGenerate, bloodGroup }) => {

    const uploadRef = useRef(null);

    const handleSelectImage = _ => {
        uploadRef.current?.click();
    }

    const handleImageFromPicker = e => {
        const { files } = e.target;
        if (!files || !files.length) return;
        const [file] = files
        if (file) {
            const url = URL.createObjectURL(file);
            setFileUrl(url);
        }
    }

    if (isGenerate && fileUrl)
        return (
            <>
                <Loader inline />
                <div style={{ transform: "translateX(200%)" }} className="fixed">
                    <div style={{ width: 1000, height: 1000 }} ref={imageRef} className="my-2 relative flex justify-center mx-auto hover:bg-gray-50 cursor-pointer border-red-700 rounded-md border-dashed w-48 h-48">
                        <img className="object-cover w-full" src={fileUrl} alt="Click to Upload" title="Click to Upload Image" />
                        <div style={{ height: 200, width: 400 }} className="flex flex-col items-center justify-evenly text-center text-7xl font-bold bg-red-700 absolute bottom-0 text-white rounded-t-full">
                            <h5 style={{ paddingBottom: 10 }}>{bloodGroup}</h5>
                            <div>
                                <h5 className="text-base font-normal" style={{ marginTop: 10 }}>#IAmThere</h5>
                                <h5 className="text-base font-normal" style={{ marginTop: 5 }}>#BloodAssist</h5>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )

    if (fileUrl)
        return (
            <>
                <div className="hidden">
                    <input onChange={handleImageFromPicker} accept="image/*" ref={uploadRef} type="file" />
                </div>
                <div ref={imageRef} onClick={handleSelectImage} className="my-2 relative flex justify-center mx-auto hover:bg-gray-50 cursor-pointer border-red-700 rounded-md border-dashed w-48 h-48 border-2">
                    <img className="object-fill w-full" src={fileUrl} alt="Click to Upload" title="Click to Upload Image" />
                </div>
            </>
        )

    return (
        <div ref={imageRef} onClick={handleSelectImage} className="my-2 relative flex justify-center mx-auto hover:bg-gray-50 cursor-pointer border-red-700 rounded-md border-dashed w-48 h-48 border-2">
            <div className="hidden">
                <input onChange={handleImageFromPicker} accept="image/*" ref={uploadRef} type="file" />
            </div>
            <div className="flex flex-col flex-1 items-center justify-evenly">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-14 w-14 text-red-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
                <p>Click to Select Image</p>
            </div>
        </div>
    );
}

export default SelectImagePromo;