import { connect } from "react-redux";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";
import NavBar from "../components/NavBar";
import ScrollToTop from "../components/ScrollToTop";
import FooterAuth from "../components/FooterAuth";
import EmailVerificationPage from "./../pages/EmailVerificationPage"
import EmailVerificationVerifyPage from "../pages/EmailVerificationVerifyPage";
import PhoneVerificationPage from "../pages/PhoneVerificationPage";
import PhoneVerificationVerifyPage from "../pages/PhoneVerificationVerifyPage";
import SomethingWentWrongPage from "../pages/SomethingWentWrongPage";
import ProfilePage from "../pages/ProfilePage";
import ProfileUpdatePage from "../pages/ProfileUpdatePage";
import RequestsPage from "../pages/RequestsPage";
import RequestsNewPage from "../pages/RequestsNewPage";
import RequestsViewPage from "../pages/RequestsViewPage";
import PublicProfilePage from "../pages/PublicProfilePage";
import AboutPage from "../pages/AboutPage";
import DonatePage from "../pages/DonatePage";
import DonorSearchPage from "../pages/DonorSearchPage";
import DonorSearchView from "../pages/DonorSearchView";
import IAmThereCampaignPage from "../pages/Promos/IAmThere";

const AuthRoutes = ({ isEmailVerified, phoneNumber }) => {

    return (
        <Router>
            <NavBar />
            <ScrollToTop />
            <Switch>
                <Route exact path="/iamthere" component={IAmThereCampaignPage} />
                <Route exact path="/about" component={AboutPage} />
                <Route exact path="/donate" component={DonatePage} />
                <Route exact path="/something-went-wrong" component={SomethingWentWrongPage} />
                {!isEmailVerified && <Route exact path="/auth/email-verification/verify" component={EmailVerificationVerifyPage} />}
                {!isEmailVerified && <Route exact path="/auth/email-verification" component={EmailVerificationPage} />}
                {!isEmailVerified && <Redirect to="/auth/email-verification" />}
                {!phoneNumber && <Route exact path="/auth/phone-verification/verify" component={PhoneVerificationVerifyPage} />}
                {!phoneNumber && <Route exact path="/auth/phone-verification" component={PhoneVerificationPage} />}
                {!phoneNumber && <Redirect to="/auth/phone-verification" />}
                <Route exact path="/donors/search" component={DonorSearchPage} />
                <Route exact path="/donors/:place" component={DonorSearchView} />
                <Redirect exact from="/donors" to="/donors/search" />
                <Route exact path="/requests/new" component={RequestsNewPage} />
                <Route path="/requests/:requestId" component={RequestsViewPage} />
                <Redirect from="/r/:id" to="/requests/:id" exact />
                <Route exact path="/requests" component={RequestsPage} />
                <Route exact path="/users/:userId" component={PublicProfilePage} />
                <Route exact path="/profile/update" component={ProfileUpdatePage} />
                <Route exact path="/profile" component={ProfilePage} />
                <Redirect to="/requests" exact />
            </Switch>
            <FooterAuth />
        </Router>
    )
}

const mapStateToProps = state => ({
    isEmailVerified: state.auth.emailVerified,
    phoneNumber: state.auth.phone,
})

export default connect(mapStateToProps)(AuthRoutes);