const OutlineButton = ({ onClick, className, value, type }) => {
    return (
        <button
            type={type}
            onClick={onClick}
            className={"border-red-600 border-2 hover:bg-red-600 hover:text-white duration-150 transition-all shadow-md hover:shadow-lg select-none transform active:translate-y-0.5 px-5 py-2 rounded-md text-red-600 " + className}>
            {value}
        </button>
    );
}

export default OutlineButton;