import React, { useState } from 'react';
import OutlineButton from './OutlineButton';
import Button from './Button';
import phoneIcon from "./../assets/images/phone.jpg";
import errorIcon from "./../assets/images/error.svg";
import doneIcon from "./../assets/images/done.svg";
import solvedIcon from "./../assets/images/solved.svg";
import { updateRequestById } from '../utils/database';
import noAvatarIcon from "./../assets/images/no_avatar.png";
import { getRequestStatus } from '../utils/validator';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { copyTextForSharing } from '../utils/validator';
import { timeStampValue } from '../utils/firebase';


const STATUS = {
    SPAM: { title: "Spam Request", image: errorIcon, description: "This request has been marked as Spam" },
    VERIFIED: { title: "Request Verified", image: doneIcon, description: "This request has been verified by the volunteer" },
    ALREADY_SOLVED: { title: "Request Already Solved", image: solvedIcon, description: "This request has been solved already" },
    SOLVED: { title: "Request Solved", image: solvedIcon, description: "This request has been solved" }
}

const RequestsViewStatusVerification = ({ request, user, makeToNextSection }) => {

    const [additionalNote, setAdditionalNote] = useState("");
    const [selectedOption, setSelectedOption] = useState("")

    const status = getRequestStatus(request);

    const handleSubmitVerification = (_) => {
        if (selectedOption === "VERIFIED") {
            updateRequestById(request.id, { isVerified: true, isSpam: false, additionalNote: additionalNote.trim(), verifiedBy: user })
        } else if (selectedOption === "SOLVED") {
            updateRequestById(request.id, { isVerified: true, isSpam: false, status: "SOLVED", additionalNote: additionalNote.trim(), verifiedBy: user, closedBy: user, closedOn: timeStampValue() })
        } else {
            updateRequestById(request.id, { isVerified: true, isSpam: true, status: "SOLVED", additionalNote: additionalNote.trim(), verifiedBy: user, closedBy: user, closedOn: timeStampValue() })
        }
    }

    const handleShareToWorld = () => {
        copyTextForSharing(request);
    }


    if (request.isVerified)
        return (
            <main className="flex items-center flex-col md:flex-row my-1">
                <div className="my-2 text-center w-full">
                    <img className="h-28 md:h-36 mx-auto my-2 w-auto" src={STATUS[status].image} alt="Verification Done" />
                    <p className="text-center text-gray-800 mx-5 md:mx-10">{STATUS[status].title}</p>
                    <p className="text-xs text-center text-gray-500 mx-0 md:mx-10">{STATUS[status].description}</p>
                </div>
                <div className="flex flex-col items-center w-full justify-center my-2 md:border-l-2 md:pl-5">
                    <div className="flex flex-col mx-5 items-start w-full">
                        <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="notes">Additional Note</label>
                        <p className="text-sm md:text-base text-gray-500 font-medium" id="notes">
                            {(request.additionalNote && request.additionalNote.split("\n").map((e, i) => <p key={i}>{e}</p>)) || "N/A"}
                        </p>
                    </div>
                    <div className="flex flex-col mx-5 items-start w-full">
                        <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="verifiedBy">Verified by</label>
                        <a href={`/users/${request.verifiedBy?.id}`} target="_blank" rel="noreferrer" className="flex items-center">
                            <img className="border-2 border-red-700 border-dashed h-8 w-8 rounded-full shadow-lg" src={request.verifiedBy?.image || noAvatarIcon} alt="Profile" />
                            <input
                                readOnly
                                defaultValue={request.verifiedBy?.name}
                                className="outline-none rounded-sm ml-2 text-indigo-900 cursor-pointer w-auto" id="verifiedBy"
                            />
                        </a>
                    </div>
                    {
                        status === "VERIFIED" &&
                        <button
                            onClick={makeToNextSection}
                            className="bg-blue-600 shadow-md transform hover:bg-blue-700 hover:shadow-lg select-none w-full mt-5 active:translate-y-0.5 px-5 py-2 rounded-md text-white">Proceed to Next</button>
                    }
                </div>
            </main>
        )

    if (request.userId === user.id) {
        return (
            <main className="flex items-center flex-col md:flex-row my-1">
                <div className="my-2 text-center w-full">
                    <img className="h-36 md:h-44 mx-auto my-2 w-auto" src={phoneIcon} alt="Call Patient" />
                    <p className="text-lg text-center text-gray-500 mx-5 md:mx-10">Verification Pending</p>
                    <p className="text-sm text-center text-gray-500 mx-0 md:mx-10">Waiting for Verification from Volunteer Side</p>
                    <p className="text-sm text-center text-gray-500 mx-0 md:mx-10">Until then, keep spreading the link</p>
                    <Button onClick={handleShareToWorld} value="Share to the World" className="my-2 mx-auto w-2/3" />
                </div>
            </main>
        )
    }


    return (
        <main className="flex items-center flex-col md:flex-row my-1">
            <div className="my-2 text-center w-full">
                <img className="h-36 md:h-44 mx-auto my-2 w-auto" src={phoneIcon} alt="Call Patient" />
                <p className="text-sm text-center text-gray-500 mx-5 md:mx-10">Contact and Verify the Request</p>
                <p className="text-xs text-center text-gray-500 mx-0 md:mx-10">Collect additional information if possible and post it here</p>
                <a href={"tel:+91" + request.phone} className="w-full md:w-1/2 mx-auto my-3 block" >
                    <OutlineButton value="Call Patient" className="w-full" />
                </a>
            </div>
            <div className="flex flex-col items-center w-full justify-center my-2 md:border-l-2 md:pl-5">
                <div className="flex flex-col mx-5 items-start w-full">
                    <label className="text-xs md:text-sm my-2 text-gray-600" htmlFor="notes">Additional Note</label>
                    <textarea
                        value={additionalNote}
                        onChange={({ target: { value } }) => setAdditionalNote(value)}
                        className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm pb-16" placeholder="Enter any Additional Info about the Patient" id="notes" />
                </div>
                <p className="text-center text-gray-500 mx-5 md:mx-10 my-3">What's the status ?</p>
                <div className="flex flex-col sm:flex-row w-full gap-2">
                    <button onClick={_ => setSelectedOption("VERIFIED")} className="bg-blue-600 shadow-md transform hover:bg-blue-700 hover:shadow-lg select-none w-full my-1 active:translate-y-0.5 px-5 py-2 rounded-md text-white">Verified</button>
                    <button onClick={_ => setSelectedOption("SOLVED")} className="bg-green-600 shadow-md transform hover:bg-green-700 hover:shadow-lg select-none w-full my-1 active:translate-y-0.5 px-5 py-2 rounded-md text-white">Solved</button>
                    <button onClick={_ => setSelectedOption("SPAM")} className="bg-red-600 shadow-md transform hover:bg-red-700 hover:shadow-lg select-none w-full my-1 active:translate-y-0.5 px-5 py-2 rounded-md text-white">Spam</button>
                </div>
            </div>
            <Dialog
                fullWidth
                open={!!selectedOption}
                onClose={_ => setSelectedOption("")}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">Confirmation</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Are you sure about this is {selectedOption}?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <div className="py-3 flex flex-col w-full">
                        <Button onClick={handleSubmitVerification} value="Yes" className="my-2 mx-auto w-2/3" />
                        <Button onClick={_ => setSelectedOption("")} value="No" className="my-2 mx-auto w-2/3" />
                    </div>
                </DialogActions>
            </Dialog>
        </main>
    );
}



export default RequestsViewStatusVerification;