import React from 'react';
import MomentUtils from '@date-io/moment';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';

const DateTime = ({ className, value, onChange }) => {
    return (
        <MuiPickersUtilsProvider utils={MomentUtils}>
            <DatePicker
                className={className}
                disableFuture
                format="DD/MM/YYYY"
                value={value}
                onChange={onChange}
                InputProps={{ disableUnderline: true, className: "rounded-sm outline-none ring-2 ring-gray-400 focus:ring-red-500 w-full p-1 pl-3" }}
                showTodayButton
            />
        </MuiPickersUtilsProvider>
    );
}

export default DateTime;