import React, { useState } from 'react';
import { Link, useHistory } from "react-router-dom"
import Button from "./../components/Button";
import OutlineButton from "./../components/OutlineButton";
import otpIcon from "./../assets/images/otp.svg";
import firebase, { auth } from "./../utils/firebase";

const PhoneVerificationVerifyPage = () => {

    const [otpCode, setOtpCode] = useState("");
    const [formError, setFormError] = useState({ otpCode: "" });
    const [isLoading, setIsLoading] = useState(false);
    const pageRouter = useHistory()
    const confirmationResult = window.confirmationResult;

    if (!confirmationResult) {
        pageRouter.replace("/something-went-wrong");
        return <></>
    }

    const handleVerifyOTP = (e) => {
        e.preventDefault();

        if (otpCode.length !== 6) return setFormError({ otpCode: "Enter a valid 6-digit code" })

        setIsLoading(true);

        const credential = firebase.auth.PhoneAuthProvider.credential(confirmationResult.verificationId, otpCode);

        auth.currentUser.linkWithCredential(credential).then(result => {
            window.location.href = "/";
        }).catch(({ code }) => {
            setIsLoading(false);
            if (code === "auth/code-expired")
                return setFormError({ otpCode: "Code Expired! Verification Failed!" })
            if (code === "auth/credential-already-in-use")
                return setFormError({ otpCode: "This phone number is already associated with another account!" })
            return setFormError({ otpCode: "Invalid OTP Code! Verification Failed!" })
        });

    }

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <form onSubmit={handleVerifyOTP} className="max-w-md shadow-lg mx-auto px-2 py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl text-red-600">You are one step ahead!</h2>
                    <img className="h-40 mx-auto my-2" src={otpIcon} alt="Phone Number" />
                    <h2 className="font-medium text-lg">OTP Verification</h2>
                    <p className="text-sm text-gray-500">OTP has been sent to the given number</p>
                    <div className="my-3 flex flex-col mx-5 items-start">
                        <label className="text-xs my-2 text-gray-600" htmlFor="code">Code</label>
                        <div className="rounded-sm w-full text-sm flex items-center ring-2 ring-gray-400">
                            <input
                                value={otpCode}
                                onChange={({ target: { value } }) => setOtpCode(value)}
                                type="number"
                                className="rounded-sm outline-none focus:ring-red-500 flex-1 h-full p-3"
                                placeholder="Enter OTP Code" id="code" />
                        </div>
                        <span className="text-xs mt-2 text-red-600 font-medium">{formError["otpCode"]}</span>
                    </div>
                    <div className="px-5">
                        <Button isLoading={isLoading} type="submit" className="mb-3 w-full" value="Verify OTP" />
                        <Link to="/">
                            <OutlineButton type="button" className="w-full" value="Try again" />
                        </Link>
                    </div>
                </form>
            </section>
        </div>
    );
}

export default PhoneVerificationVerifyPage;