import React, { useState } from 'react';
import SpinnerIcon from "./Icons/Spinner";
import firebase from "./../utils/firebase"
import { useTranslation } from "react-i18next";

const GoogleSignInButton = () => {

    const [isLoading, setIsLoading] = useState(false);
    const { t } = useTranslation("Auth")

    const handleSignInWithGoogle = () => {
        setIsLoading(true);

        const provider = new firebase.auth.GoogleAuthProvider();
        firebase.auth().signInWithRedirect(provider)
            .then(_ => { })
            .catch(_ => {
            });

    }

    return (
        <button onClick={handleSignInWithGoogle} type="button" className="bg-blue-500 text-white hover:text-gray-200 flex items-center justify-center cursor-pointer border-blue-500 border-2 shadow-md hover:bg-blue-800 hover:border-blue-800 hover:shadow-lg select-none transform active:translate-y-0.5 px-5 py-2 rounded-md w-full">
            {isLoading ?
                <SpinnerIcon className="h-5 w-5 mx-auto animate-spin" />
                : <>
                    <svg className="w-5 h-5 fill-current" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 30 30" width="30px" height="30px">    <path d="M 15.003906 3 C 8.3749062 3 3 8.373 3 15 C 3 21.627 8.3749062 27 15.003906 27 C 25.013906 27 27.269078 17.707 26.330078 13 L 25 13 L 22.732422 13 L 15 13 L 15 17 L 22.738281 17 C 21.848702 20.448251 18.725955 23 15 23 C 10.582 23 7 19.418 7 15 C 7 10.582 10.582 7 15 7 C 17.009 7 18.839141 7.74575 20.244141 8.96875 L 23.085938 6.1289062 C 20.951937 4.1849063 18.116906 3 15.003906 3 z" /></svg>
                    <span className="ml-2">{t("Sign In with Google")}</span>
                </>
            }

        </button>
    );
}

export default GoogleSignInButton;