import React, { useState } from 'react';
import Button from "./../components/Button";
import OutlineButton from "./../components/OutlineButton";
import { Link } from "react-router-dom"
import privacyIcon from "./../assets/images/privacy.svg";
import doneIcon from "./../assets/images/done.svg";
import { auth } from "./../utils/firebase";
import { useTranslation } from "react-i18next";

const ForgotPasswordPage = () => {

    const [email, setEmail] = useState("");
    const [formError, setFormError] = useState({ email: "" });
    const [isLoading, setIsLoading] = useState(false);
    const [isMailSent, setIsMailSent] = useState(false);
    const { t } = useTranslation("Auth");

    const handleSendPasswordReset = (e) => {
        e.preventDefault();

        if (!email) return setFormError({ email: "Enter a valid Email!" });
        setFormError({ email: "" });

        setIsLoading(true);

        auth.sendPasswordResetEmail(email).then(_ => {
            setIsMailSent(true);
        }).catch(({ code }) => {
            setIsLoading(false);
            if (code === "auth/invalid-email")
                return setFormError({ email: "Enter a valid Email!" });
            if (code === "auth/user-not-found")
                return setFormError({ email: "You are not registered!" });
            return setFormError({ email: "Something wrong! Operation failed!" });
        });

    }

    if (isMailSent)
        return (
            <div>
                <section className="pt-5 px-10 text-center select-none">
                    <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                        <h2 className="font-medium text-xl">{t("Reset Your Password")}</h2>
                        <img className="w-40 h-40 mx-auto my-5" src={doneIcon} alt="Password" />
                        <p className="text-gray-800 px-2">{t("We have sent the instructions to reset your password")}!</p>
                        <p className="text-xs text-gray-500 px-2">{t("Check your Spam Folder if you didn't receive the mail")}</p>
                        <Link to="/" className="my-2 mt-5 flex flex-col mx-5 items-start">
                            <OutlineButton className="w-full" value={t("Continue")} />
                        </Link>
                    </div>
                </section>
            </div>
        );

    return (
        <div>
            <section className="pt-5 px-10 text-center select-none">
                <div className="max-w-md shadow-lg mx-auto py-10 rounded-md bg-white">
                    <h2 className="font-medium text-xl">{t("Reset Your Password")}</h2>
                    <img className="w-40 h-40 mx-auto my-5" src={privacyIcon} alt="Password" />
                    <form onSubmit={handleSendPasswordReset}>
                        <div className="my-2 flex flex-col mx-5 items-start">
                            <label className="text-xs my-2 text-gray-600" htmlFor="email">{t("Email Address")}</label>
                            <input
                                type="email"
                                className="ring-2 ring-gray-400 focus:ring-red-500 outline-none rounded-sm p-2 w-full text-sm"
                                onChange={({ target: { value } }) => setEmail(value)}
                                value={email}
                                placeholder={t("Enter Your Email")}
                                id="email" />
                            <span className="text-xs mt-2 text-red-600 font-medium">{formError["email"]}</span>
                        </div>
                        <div className="my-3 flex flex-col mx-5 items-start">
                            <Button isLoading={isLoading} className="w-full" value={t("Send Instructions")} />
                        </div>
                        <Link to="/auth/login" className="my-2 flex flex-col mx-5 items-start">
                            <OutlineButton className="w-full" value={t("Login")} />
                        </Link>
                    </form>
                </div>
            </section>
        </div>
    );
}

export default ForgotPasswordPage;