import React, { useState } from 'react';
import RequestsViewVerification from './RequestsViewVerification';
import RequestsViewSearch from './RequestsViewSearch';
import RequestsViewFinal from './RequestsViewFinal';
import RequestsViewStatusVerification from './RequestsViewStatusVerification';
import RequestsViewStatusSearch from './RequestsViewStatusSearch';
import RequestsViewStatusDone from './RequestsViewStatusDone';
import { useTranslation } from "react-i18next";

const RequestsViewStatus = ({ request, user }) => {

    const [currentSelected, setCurrentSelected] = useState(0);
    const { t } = useTranslation("Requests")

    const isVerified = request.isVerified;
    const isSpam = request.isSpam;
    const isUnitsGot = request.unitsGot >= request.units
    const isSolved = request.status === "SOLVED"

    const isFirstStepCompleted = isVerified;
    const isSecondStepCompleted = isSpam || isUnitsGot || isSolved;
    const isThirdStepCompleted = isSolved;

    return (
        <section className="p-5 mt-2 sm:px-10 select-none bg-white mx-5 rounded-lg shadow-lg">
            <h2 className="text-lg font-semibold">{t("Status")}</h2>
            <div className="w-10 bg-red-700 h-1 my-2" />
            <div className="mt-5 bg-white lg:border-t lg:border-b lg:border-gray-200">
                <nav className="mx-auto max-w-7xl" aria-label="Progress">
                    <ol className="rounded-md overflow-hidden lg:flex lg:border-l lg:border-r lg:border-gray-200 lg:rounded-none">
                        <RequestsViewVerification
                            verifiedBy={request.verifiedBy?.name}
                            isCompleted={isFirstStepCompleted}
                            setCurrentSelected={_ => setCurrentSelected(0)}
                        />
                        <RequestsViewSearch
                            disabled={!isVerified}
                            isCurrent={currentSelected === 1}
                            setCurrentSelected={_ => setCurrentSelected(1)}
                            isCompleted={isSecondStepCompleted}
                        />
                        <RequestsViewFinal
                            disabled={!isVerified}
                            isCurrent={currentSelected === 2}
                            setCurrentSelected={_ => setCurrentSelected(2)}
                            isCompleted={isThirdStepCompleted}
                        />
                    </ol>
                </nav>
            </div>
            {(currentSelected === 0) &&
                <RequestsViewStatusVerification
                    user={user}
                    makeToNextSection={_ => setCurrentSelected(1)}
                    request={request}
                />
            }
            {(currentSelected === 1) &&
                <RequestsViewStatusSearch
                    user={user}
                    makeToNextSection={_ => setCurrentSelected(2)}
                    request={request}
                    isUnitsGot={isUnitsGot}
                    isSolved={isSolved}
                />
            }
            {(currentSelected === 2) &&
                <RequestsViewStatusDone
                    user={user}
                    request={request}
                    isSolved={isSolved}
                />
            }
        </section>
    );
}

export default RequestsViewStatus;