import React, { useEffect, useState } from 'react';
import moment from "moment";
import { useTranslation } from 'react-i18next';

const RequestTimer = ({ needWithin }) => {

    const timerEnd = moment(needWithin * 1000);
    // const timerEnd = moment().add(10, 'seconds');
    const getCountdownTime = () => {
        const difference = timerEnd.diff(moment());
        if (moment.duration(difference).asSeconds() <= 0)
            return 0;
        else
            return (moment.duration(difference).humanize())
    }
    const { t } = useTranslation("Requests")
    const [currentTime, setCurrentTime] = useState(getCountdownTime())

    useEffect(() => {

        const intervalId = setInterval(() => {
            const difference = timerEnd.diff(moment());
            if (moment.duration(difference).asSeconds() <= 0) {
                setCurrentTime(0)
                clearInterval(intervalId)
            }
            else
                setCurrentTime(moment.duration(difference).humanize())
        }, 1000);

        return () => {
            clearInterval(intervalId);
        }

    }, [timerEnd])

    if (currentTime === 0) return <span className="font-medium">{t("Time Over")}</span>

    return (
        <>
            Time Left: {currentTime}
        </>
    );
}

export default RequestTimer;