import { LANGUAGES } from "./../utils/config";
import { useTranslation } from 'react-i18next';

const LanguageSelector = ({ className }) => {

    const { i18n } = useTranslation("Common");

    const handleChangeLanguage = _ => {
        if (i18n.language === "en-US")
            return i18n.changeLanguage("ta")
        if (i18n.language === "ta")
            return i18n.changeLanguage("en-US")
    }

    return (
        <>
            <select
                value={i18n.language}
                onChange={({ target: { value } }) => i18n.changeLanguage(value)}
                className={"h-10 px-5 appearance-none hidden rounded-md md:block bg-gray-100 focus:outline-none " + className}>
                {Object.keys(LANGUAGES).map(e => <option key={e} value={e}>{LANGUAGES[e].name}</option>)}
            </select>
            <div onClick={handleChangeLanguage} className={"h-10 w-10 bg-gray-100 hover:bg-gray-200 md:hidden flex items-center cursor-pointer justify-center rounded-full font-bold text-indigo-900 " + className}>
                {i18n.language === "en-US" && "அ"}
                {i18n.language === "ta" && "A"}
            </div>
        </>
    );
}

export default LanguageSelector;