import React from 'react';
import Button from "./Button";
import { removeDonorByRequestId } from '../utils/database';

const RequestsViewDonorsList = ({ donors, requestId, currentUser, isSolved }) => {

    const handleDeleteDonor = id => {

        removeDonorByRequestId(requestId, id)
            .then(_ => { })
            .catch(err => { console.log(err) })
            .finally(_ => { })

    }


    if (donors.length === 0)
        return (
            <div className="text-center my-5 text-sm md:text-base  text-gray-600">
                {
                    isSolved ? "No donors added. Request might be solved offline"
                        : "No donors added. Be the first to contribute"
                }
            </div>
        )

    return (
        <div>
            {
                donors.map((e, i) => (
                    <div key={i} className="w-full p-2 ring-1 my-2 rounded-md flex flex-col md:flex-row items-center justify-between">
                        <p className="font-medium md:flex-1">{e.fullName}</p>
                        <div className="flex md:flex-1">
                            <a href={"tel:+91" + e.phone} className="text-indigo-900 font-semibold">+91{e.phone}</a>
                        </div>
                        <p className="md:flex-1">{e.units} Units</p>
                        {
                            !isSolved && (currentUser.id === e.addedBy) &&
                            <div className="flex justify-end md:flex-1">
                                < Button onClick={_ => handleDeleteDonor(e.id)} value="Delete" className="pt-1 pb-1" />
                            </div>
                        }
                    </div>
                ))
            }
        </div>
    );
}

export default RequestsViewDonorsList;