import { useParams } from "react-router-dom";
import { Redirect, Link } from "react-router-dom";
import DonorsListWithFilter from "../components/Donors/DonorsListWithFilter";
import { humanize } from "../utils/parser";
import DocumentTitle from "./../components/Extras/Header";

const DonorSearchView = () => {

    const { place } = useParams();

    if (!place) return <Redirect to="/donors/search" />

    const district = humanize(place)

    return (
        <section className="p-5 sm:px-10 select-none bg-white mx-5 rounded-lg shadow-lg relative">
            <DocumentTitle title={`Donors in ${district}`} />
            <h2 className="text-lg font-semibold">Search Donors</h2>
            <div className="w-24 bg-red-700 h-1 my-2" />
            <Link to="/donors/search">
                <button className="absolute right-0 top-0 select-none active:translate-y-0.5 mr-5 mt-5 rounded-md text-indigo-700 font-semibold">Back</button>
            </Link>
            <h5 className="my-2">Showing result for {district}</h5>
            <DonorsListWithFilter district={place} />
        </section>
    );
}

export default DonorSearchView;