import React from 'react';
import AuthRoutes from "./routes/auth";
import PublicRoutes from "./routes/public";
import { setUserData, updateUserData } from "./cache/auth"
import { useEffect } from "react";
import { auth } from "./utils/firebase";
import { connect } from "react-redux";
import Loader from "./components/Loader"
import { getInitialUserData } from './utils/generator';
import { streamUserDataByID, updateUserDataByID } from './utils/database';

const App = ({
  isLoggedIn,
  setInitialUserData,
  updateUser
}) => {

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if (user !== null) {
        const userData = getInitialUserData(user);
        setInitialUserData(userData);

        updateUserDataByID(userData.id, userData);

        streamUserDataByID(userData.id, event => {
          updateUser(event.val())
        }, _ => { });

      } else {
        setInitialUserData({ id: null })
      }
    });

  }, [setInitialUserData, updateUser])

  if (isLoggedIn === null)
    return <Loader />;

  if (isLoggedIn)
    return <AuthRoutes />;

  return <PublicRoutes />;

}

const mapStateToProps = state => ({
  isLoggedIn: state.auth.isLoggedIn,
})

const mapDispatchToProps = dispatch => ({
  setInitialUserData: user => dispatch(setUserData(user)),
  updateUser: user => dispatch(updateUserData(user))
})

export default connect(mapStateToProps, mapDispatchToProps)(App);