import React from 'react';
import MomentUtils from '@date-io/moment';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';

const DateTime = ({ className, value, onChange }) => {
    return (
        <MuiPickersUtilsProvider utils={MomentUtils}>
            <DateTimePicker
                className={className}
                disablePast
                format="DD/MM/YYYY hh:mm A"
                value={value}
                onChange={onChange}
                InputProps={{ disableUnderline: true, className: "rounded-sm outline-none ring-2 ring-gray-400 focus:ring-red-500 w-full p-2 pl-12" }}
                showTodayButton
            />
        </MuiPickersUtilsProvider>
    );
}

export default DateTime;