import React from 'react';
import CompletedStep from './CompletedStep';
import CurrentStep from './CurrentStep';
import UpcomingStep from './UpcomingStep';

const RequestsViewSearch = ({ isCurrent, isCompleted, setCurrentSelected, disabled }) => {

    if (isCompleted)
        return <CompletedStep setCurrentSelected={setCurrentSelected} title="Community Search" description="Let's find the donor" />;

    if (isCurrent)
        return <CurrentStep setCurrentSelected={setCurrentSelected} stepNo={2} title="Community Search" description="Let's find the donor" />;

    return <UpcomingStep disabled={disabled} setCurrentSelected={setCurrentSelected} stepNo={2} title="Community Search" description="Let's find the donor" />;

}

export default RequestsViewSearch;