import { getCopyText, getDonorCopyText } from "./generator"

export const getAgeFromDOB = dob => {

    if (!dob) return "N/A";
    const date = new Date(dob);
    const diff = Date.now() - date.getTime();
    const ageDate = new Date(diff);

    return Math.abs(ageDate.getUTCFullYear() - 1970);
}

export const isEmpty = arr => arr.length === 0;


export const getGender = (gender) => {
    switch (gender) {
        case "M":
            return "Male"
        case "F":
            return "Female"
        case "T":
            return "Transgender"
        default:
            return gender
    }
}

export const getRequestStatus = request => {

    const isVerified = request.isVerified;
    const isSpam = request.isSpam;
    const isUnitsGot = request.unitsGot >= request.units
    const isSolved = request.status === "SOLVED"
    const isAlreadySolved = isVerified && isSolved && !isUnitsGot

    if (isVerified && isSpam) return "SPAM"

    if (isAlreadySolved) return "ALREADY_SOLVED";

    if (isVerified && isUnitsGot && isSolved) return "SOLVED";

    if (isVerified) return "VERIFIED";

}


function fallbackCopyTextToClipboard(text) {
    const textArea = document.createElement("textarea");
    textArea.value = text;

    // Avoid scrolling to bottom
    textArea.style.top = "0";
    textArea.style.left = "0";
    textArea.style.position = "fixed";

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();

    try {
        document.execCommand('copy');
    } catch (_) {
    }

    document.body.removeChild(textArea);
}

export const copyTextForSharing = request => {

    let text = getCopyText(request);
    const shortLink = request.shortLink || "https://bloodassist.org/requests/" + request.id
    if (navigator.share) {
        const title = `Need ${request.units} Units of ${request.bloodGroup} Blood | Blood Assist`
        const desc = text;
        navigator.share({ title: title, text: desc, url: shortLink })
            .catch(err => console.log(err))
    }

    if (!navigator.clipboard)
        fallbackCopyTextToClipboard(text);
    else
        navigator.clipboard.writeText(text)
            .then(() => { }, (_) => { });
}

export const copyDonorForSharing = donor => {

    let text = getDonorCopyText(donor);
    const shortLink = "https://bloodassist.org/users/" + donor.userId
    if (navigator.share) {
        const title = "🃏 Blood Donor Details | Blood Assist"
        const desc = text;
        navigator.share({ title: title, text: desc, url: shortLink })
            .catch(err => console.log(err))
    }

    if (!navigator.clipboard)
        fallbackCopyTextToClipboard(text);
    else
        navigator.clipboard.writeText(text)
            .then(() => { }, (_) => { });
}


export function paginateItems(
    totalItems,
    currentPage = 1,
    pageSize = 10,
    maxPages = 10
) {
    // calculate total pages
    let totalPages = Math.ceil(totalItems / pageSize);

    // ensure current page isn't out of range
    if (currentPage < 1) {
        currentPage = 1;
    } else if (currentPage > totalPages) {
        currentPage = totalPages;
    }

    let startPage, endPage;
    if (totalPages <= maxPages) {
        // total pages less than max so show all pages
        startPage = 1;
        endPage = totalPages;
    } else {
        // total pages more than max so calculate start and end pages
        let maxPagesBeforeCurrentPage = Math.floor(maxPages / 2);
        let maxPagesAfterCurrentPage = Math.ceil(maxPages / 2) - 1;
        if (currentPage <= maxPagesBeforeCurrentPage) {
            // current page near the start
            startPage = 1;
            endPage = maxPages;
        } else if (currentPage + maxPagesAfterCurrentPage >= totalPages) {
            // current page near the end
            startPage = totalPages - maxPages + 1;
            endPage = totalPages;
        } else {
            // current page somewhere in the middle
            startPage = currentPage - maxPagesBeforeCurrentPage;
            endPage = currentPage + maxPagesAfterCurrentPage;
        }
    }

    // calculate start and end item indexes
    let startIndex = (currentPage - 1) * pageSize;
    let endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

    // create an array of pages to ng-repeat in the pager control
    let pages = Array.from(Array((endPage + 1) - startPage).keys()).map(i => startPage + i);

    // return object with all pager properties required by the view
    return {
        totalItems: totalItems,
        currentPage: currentPage,
        pageSize: pageSize,
        totalPages: totalPages,
        startPage: startPage,
        endPage: endPage,
        startIndex: startIndex,
        endIndex: endIndex,
        pages: pages
    };
}

export const downloadFromLink = (url, name) => {
    const link = document.createElement("a");
    if (("download" in link)) {
        link.download = name;
        link.href = url;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    } else {
        const image = new Image();
        image.src = url;
        const w = window.open("");
        w.document.write(`<h1>Right Click / Long Press and Save Image</h1>` + image.outerHTML);
    }
}